import { Buffer } from 'buffer';
import cloneDeep from 'just-clone';

import { isPrintingWithLineFeedCommand, possiblyDividerText, textToKeyValue, trySplitTextData } from '../analyzer';
import { PrinterContext } from '../renderer/printer';
import { walkAST } from '../renderer/util';
import { AnalyzerGuide, AnalyzerNode, ChunkData, DataChunk, TableChunk } from '../types';

export const convertTextToBinaryFormat = (textFormatData: string) => {
  let binaryData = '';
  for (const char of textFormatData.split('')) {
    const encodedChar = encodeURIComponent(char);
    const isCharURIEncoded = encodedChar.match(/%.{2}/g)?.length;

    if (!isCharURIEncoded) binaryData += encodedChar;
    else {
      binaryData += String.fromCharCode(parseInt(encodedChar.replace('%', ''), 16));
    }
  }

  return Buffer.from(binaryData);
};

export const convertBufferToStringBytes = (bufferData: Buffer) => {
  const bytes: string[] = [];
  bufferData.forEach((element) => {
    bytes.push(String.fromCharCode(element));
  });

  return bytes;
};

/**
 * TODO:
 * This could be improved later, current plan for improvement is that we can
 * keep track of all analyzerGuide that already has a matching analyzed data
 * and so for each line (un-analyzed data) we could try searching for all
 * unmatched analyzerGuide and see if we could find a match.
 *
 * This algorithm can then make sure that our analyzer can still extract data
 * correctly given the position of each line in the receipt data changed, for
 * example:
 *
 * - Receipt should have look like this:
 *   "title   :    test  "
 *   "employee:    tost  "
 *   "price   :    USD100"
 *
 * - But instead this is what we got:
 *   "employee:    tost  "
 *   "price   :    USD100"
 *   "title   :    test  "
 *
 * As you can see the position of the receipt data changed in the second receipt.
 */
export const convertEscPosToJsonObject = ({
  ast,
  possibleMaxWidth,
  analyzerGuide = undefined,
}: {
  ast: ChunkData[];
  possibleMaxWidth: number;
  analyzerGuide?: AnalyzerGuide;
}) => {
  const unparsedTexts: {
    text: string;
    lineNumber: number;
  }[] = [];
  const parsedObjects = {};

  let lineNumber = 0;
  let parsedLines = 0;

  let titleCharacterPositions = [];
  let parsedTableData: Record<string, any>[] = [];
  let tempParsedTableData: Record<string, any>[][] = [[]];
  let currentTableChunk: TableChunk = undefined;

  for (const [, node, textData] of walkAST(ast, new PrinterContext())) {
    if (isPrintingWithLineFeedCommand(node.command)) lineNumber += 1;
    if (textData.length === 0 || possiblyDividerText(textData, possibleMaxWidth)) continue;

    if (parsedLines > analyzerGuide.length) {
      /**
       * Failed to follow analyzer guide so return parsed data by far.
       */

      if (parsedTableData.length > 0) {
        Object.assign(parsedObjects, {
          [currentTableChunk.node]: parsedTableData,
        });
      }

      return {
        parsedData: {
          ...parsedObjects,
        },
        unparsedData: unparsedTexts,
      };
    }

    let analyzerNode: AnalyzerNode = undefined;
    if (analyzerGuide) {
      analyzerNode = analyzerGuide[parsedLines];
    }

    let keyValuePairResult = undefined;

    if ((analyzerNode && !Array.isArray(analyzerNode) && analyzerNode.chunkType === 'tableChunk') || currentTableChunk !== undefined) {
      analyzerNode = analyzerNode as TableChunk;
      currentTableChunk = analyzerNode;

      const currentTableLine = tempParsedTableData[tempParsedTableData.length - 1].length % currentTableChunk.template.length;
      const currentLineTemplate: DataChunk[] = currentTableChunk.template[currentTableLine];
      const currentTitleCharacterPositions = titleCharacterPositions[currentTableLine];
      const titleTemplateExist = currentTableChunk.titleTemplate && currentTableChunk.titleTemplate.length > 0;

      let successfullyParsed = false;
      let shouldGoToNextNode = false;

      /**
       * Run semantic analysis if a TableChunk have a title by checking the
       * position matching of character of the value texts with the title texts
       */
      if (titleTemplateExist) {
        if (titleCharacterPositions.length < currentTableChunk.template.length) {
          try {
            // Get the title character positions
            titleCharacterPositions.push(textData.split('').map((char, index) => (char !== ' ' ? [index] : [null])));

            // Combine title character positions based on splitted text
            const splittedTexts = trySplitTextData(textData, currentTableChunk.titleTemplate[currentTableLine], possibleMaxWidth);
            let charIndex = 0;

            for (const [splittedTextIndex, splittedTextData] of splittedTexts.entries()) {
              const indexOfSplittedText = textData.slice(0, splittedTextData.endPosition).lastIndexOf(splittedTextData.text.trim());
              for (let i = charIndex; i < splittedTextData.endPosition; i++) {
                if (indexOfSplittedText <= i && i <= splittedTextData.endPosition && i <= textData.length - 1) {
                  titleCharacterPositions[titleCharacterPositions.length - 1][i].push(splittedTextIndex);
                  if (titleCharacterPositions[titleCharacterPositions.length - 1][i][0] === null) {
                    titleCharacterPositions[titleCharacterPositions.length - 1][i][0] = i;
                  }
                }
              }

              charIndex = splittedTextData.endPosition;
            }

            successfullyParsed = true;
          } catch {
            // Current line doesnt match so reset the title positions
            if (titleCharacterPositions.length > 0) {
              titleCharacterPositions = [];
            }

            successfullyParsed = false;
          }
        } else {
          if (analyzerNode.endsWith && textData.match(analyzerNode.endsWith)) {
            // Ending indicator found so go to next node
            shouldGoToNextNode = true;
          }

          let currentTextMatching: {
            text: string;
            endPosition: number;
            isMatched: boolean;
          }[] = [];

          // Get matching character position of value data with the title character positions
          let currentTextPositions = [];
          for (const [index, character] of textData.split('').entries()) {
            const isLastData = index === textData.length - 1;
            if (character === ' ' || isLastData) {
              if (isLastData && character !== ' ') {
                currentTextPositions.push(index);
              }

              if (currentTextPositions.length > 0) {
                let matchedPos = -1;
                for (const [index, [titleCharPos]] of currentTitleCharacterPositions.entries()) {
                  const isPrevPosNull = index === 0 || currentTitleCharacterPositions[index - 1][0] === null;
                  const isNextPosNull = index === currentTitleCharacterPositions.length - 1 || currentTitleCharacterPositions[index + 1][0] === null;

                  // Current text position matches the first character in a title text
                  if (currentTextPositions[0] === titleCharPos && isPrevPosNull) {
                    matchedPos = index;
                    break;
                  }

                  // Current text position matches the last character in a title text
                  if (currentTextPositions[currentTextPositions.length - 1] === titleCharPos && isNextPosNull) {
                    matchedPos = index;
                    break;
                  }

                  // Current text position is inside a title text (from left side)
                  const midMatchingFromLeft = currentTitleCharacterPositions[currentTextPositions[0]][0];
                  if (midMatchingFromLeft !== null) {
                    matchedPos = midMatchingFromLeft;
                    break;
                  }

                  // Current text position is inside a title text (from right side)
                  const midMatchingFromRight = currentTitleCharacterPositions[currentTextPositions[currentTextPositions.length - 1]][0];
                  if (midMatchingFromRight !== null) {
                    matchedPos = midMatchingFromRight;
                    break;
                  }
                }

                let shouldCreateNewMatching = true;
                let titleNodeIndex = -1;

                if (matchedPos !== -1) {
                  titleNodeIndex = currentTitleCharacterPositions[matchedPos][1];

                  if (currentTextMatching[titleNodeIndex]) {
                    const matchedText = textData.slice(currentTextMatching[titleNodeIndex].endPosition, isLastData ? index + 1 : index);
                    currentTextMatching[titleNodeIndex].text += matchedText;
                    currentTextMatching[titleNodeIndex].endPosition += matchedText.length;
                    shouldCreateNewMatching = false;
                  }
                }

                if (shouldCreateNewMatching) {
                  if (titleNodeIndex !== -1) {
                    currentTextMatching[titleNodeIndex] = {
                      text: textData.slice(currentTextPositions[0], isLastData ? index + 1 : index),
                      endPosition: index,
                      isMatched: true,
                    };
                  } else {
                    currentTextMatching.push({
                      text: textData.slice(currentTextPositions[0], isLastData ? index + 1 : index),
                      endPosition: index,
                      isMatched: false,
                    });
                  }
                }

                currentTextPositions = [];
              }

              continue;
            }

            currentTextPositions.push(index);
          }

          if (currentTextMatching.some((el) => el.isMatched)) {
            // This will be false whenever there is a non matched text
            let matchedTextFound = currentTextMatching.every((el) => el.isMatched);

            if (currentTextMatching.some((el) => !el.isMatched)) {
              const tempCurrentTextMatching: {
                text: string;
                endPosition: number;
                isMatched: boolean;
              }[] = [];
              let tempNonMatchedText = '';

              // Merge other non matched text with the first closest neighbor of matched text
              for (const [index, matching] of currentTextMatching.entries()) {
                if (matching.isMatched) {
                  tempCurrentTextMatching.push(matching);
                  matchedTextFound = true;
                } else {
                  if (!matchedTextFound) {
                    // If there are any non matched text in the first datas
                    // then break early to run other analysis instead
                    break;
                  }

                  tempNonMatchedText += textData.slice(currentTextMatching[index - 1].endPosition, matching.endPosition);
                  if (index === currentTextMatching.length - 1 || currentTextMatching[index + 1].isMatched) {
                    tempCurrentTextMatching[tempCurrentTextMatching.length - 1].text += tempNonMatchedText;
                    tempCurrentTextMatching[tempCurrentTextMatching.length - 1].endPosition = matching.endPosition;
                    tempNonMatchedText = '';
                  }
                }
              }

              currentTextMatching = tempCurrentTextMatching;
            }

            if (matchedTextFound) {
              if (currentTextMatching.length !== currentLineTemplate.length) {
                // Fill in the text matching with some empty data instead
                currentTextMatching.push(undefined);
              }

              const tempMergedObject = {};
              for (const [index, matching] of currentTextMatching.entries()) {
                const currentAnalyzerNode = currentLineTemplate[index];

                if (!currentAnalyzerNode.ignore) {
                  Object.assign(tempMergedObject, {
                    [currentAnalyzerNode.node]: matching
                      ? currentAnalyzerNode.isKeyValuePair
                        ? currentAnalyzerNode.divider
                          ? Object.values(textToKeyValue(matching.text, possibleMaxWidth, undefined, [currentAnalyzerNode.divider])).length > 0
                            ? Object.values(textToKeyValue(matching.text, possibleMaxWidth, undefined, [currentAnalyzerNode.divider]))[0]
                            : null
                          : Object.values(textToKeyValue(matching.text, possibleMaxWidth)).length > 0
                          ? Object.values(textToKeyValue(matching.text, possibleMaxWidth))[0]
                          : null
                        : matching.text
                      : null,
                  });
                }
              }

              tempParsedTableData[currentTableLine].push(tempMergedObject);
              successfullyParsed = true;
            } else {
              shouldGoToNextNode = true;
            }
          }
        }
      }

      /**
       * Use general data chunk analysis for when
       *
       * 1. the table chunk doesnt have any titleTemplate, or
       * 2. table chunk position matching analysis failed
       */
      if (!successfullyParsed && !shouldGoToNextNode) {
        if (analyzerNode.endsWith && textData.match(new RegExp(analyzerNode.endsWith))) {
          // Ending indicator found so go to next node
          shouldGoToNextNode = true;
        }

        const tableData = textToKeyValue(textData, possibleMaxWidth, currentLineTemplate);
        if (Object.keys(tableData).length > 0) {
          if (currentTableLine > tempParsedTableData.length - 1) {
            tempParsedTableData.push([tableData]);
          } else {
            tempParsedTableData[currentTableLine].push(tableData);
          }
          successfullyParsed = true;
        }
      }

      // Merge all parsed data from current text data to the whole table data
      if (tempParsedTableData.length === currentTableChunk.template.length && tempParsedTableData.every((el) => el.length > 0)) {
        const tempMergedParsedTableData = {};
        for (const tempParsedTable of tempParsedTableData) {
          Object.assign(tempMergedParsedTableData, ...tempParsedTable);
        }

        parsedTableData.push(tempMergedParsedTableData);
        tempParsedTableData = [[]];
      }

      if (!successfullyParsed || shouldGoToNextNode) {
        if (parsedTableData.length > 0) {
          // Inject the table data for when it no longer finds
          // any table value data / rows
          Object.assign(parsedObjects, {
            [currentTableChunk.node]: cloneDeep(parsedTableData),
          });
          parsedTableData = [];
        }

        /**
         * Move on to next analyzer node regardless of any table data found previously or not
         * and re-call the whole data extracting analysis with the next analyzer node for
         * current textData because when this code is reached it means the current textData
         * is analyzed by the previous analyzer node (and the extraction failed since its
         * extracting the data with the wrong analyzer node)
         */
        parsedLines += 1;
        analyzerNode = analyzerGuide[parsedLines];

        titleCharacterPositions = [];
        currentTableChunk = undefined;

        keyValuePairResult = textToKeyValue(textData, possibleMaxWidth, analyzerNode);
      }
    } else {
      keyValuePairResult = textToKeyValue(textData, possibleMaxWidth, analyzerNode);
    }

    if (keyValuePairResult) {
      if (Object.keys(keyValuePairResult).length === 0) {
        if (analyzerNode && !Array.isArray(analyzerNode) && analyzerNode.chunkType === 'dataChunk' && analyzerNode.ignore) {
          parsedLines += 1;
          continue;
        }

        unparsedTexts.push({
          text: textData.trim(),
          lineNumber,
        });
      } else {
        // Move to next analyzer node only when current node successfully extracted
        // the data based on the guide
        parsedLines += 1;
        Object.assign(parsedObjects, keyValuePairResult);
      }
    }
  }

  /**
   * Inject the table data if its exist (edge case for when the
   * table chunk is in last index of the analyzer guide array)
   */
  if (parsedTableData.length > 0) {
    Object.assign(parsedObjects, {
      [currentTableChunk.node]: parsedTableData,
    });
  }

  return {
    parsedData: {
      ...parsedObjects,
    },
    unparsedData: unparsedTexts,
  };
};
