import { useClearCurrentReceipt } from '@/api/receipt';
import { useIsMutationLoading } from '@/utils/hooks/useIsMutationLoading';
import { useLocalStorageManager } from '@/utils/localstorage';
import { MutationKeys } from '@pi-xcels/graphql/constants';
import { Ref, ref, watch } from 'vue';
import { useRouter } from 'vue-router';

export function useReceiptDetailCloseHandler({ isCurrentReceipt }: { isCurrentReceipt: Readonly<Ref<boolean>> }) {
  const { mutateAsync: clearCurrentReceipt } = useClearCurrentReceipt();
  const router = useRouter();
  const isLinkReceiptToUserLoading = useIsMutationLoading([MutationKeys.LinkReceiptToUser]);
  const localStorageManager = useLocalStorageManager();
  const shouldClose = ref(false);
  const handleClose = async () => {
    shouldClose.value = true;
  };
  watch([shouldClose, isLinkReceiptToUserLoading], ([shouldClose, isLinkReceiptToUserLoading]) => {
    if (shouldClose && !isLinkReceiptToUserLoading) {
      window.scrollTo(0, 0);

      if (localStorageManager.hashedReceipt.value) {
        localStorageManager.removeHashedReceipt();
      }

      if (isCurrentReceipt?.value) {
        clearCurrentReceipt();
      }

      router.push('/');
    }
  });
  return { handleClose };
}
