import { apolloClient, handleApolloMutationResponse } from '@/utils/apollo';
import { updateEmployeeMutation } from '@pi-xcels/graphql';
import { MutationKeys } from '@pi-xcels/graphql/constants';
import { UpdateEmployeeMutationVariables } from '@pi-xcels/graphql/types';
import { useMutation } from '@tanstack/vue-query';

const useUpdateEmployee = () => {
  return useMutation({
    mutationKey: [MutationKeys.UpdateEmployee],
    mutationFn: ({ variables }: { variables: UpdateEmployeeMutationVariables }) =>
      apolloClient
        .mutate({
          mutation: updateEmployeeMutation,
          variables,
        })
        .then(handleApolloMutationResponse),
  });
};

export { useUpdateEmployee };
