import { CommandObject } from '../types';

/**
 * General Codes
 **/

export const NUL = '\x00';
export const DLE = '\x10';
export const ESC = '\x1b';
export const FS = '\x1c';
export const FF = '\x0c';
export const GS = '\x1d';
export const LF = '\x0a';
export const CR = '\x0d';
export const HT = '\x09';
export const SP = '\x20';

/**
 * Print commands and/or Basic control commands
 **/

// prettier-ignore
export const basicCommand: CommandObject = {
  [`${ESC}\x40` /* ESC @ */]: ["Initialize printer"],
  [`${FF}`      /* FF    */]: ["Print and feed paper 1 lines"],
  [`${LF}`      /* LF    */]: ["Print and line feed"],
  [`${CR}`      /* CR    */]: ["Print and carriage return"],
  [`${ESC}\x4a` /* ESC J */]: ["Print and feed paper using minimum units", 1],
  [`${ESC}\x64` /* ESC d */]: ["Print and feed paper n lines", 1],
  [`${HT}`      /* HT    */]: ["Horizontal tab"],
};

/**
 * Character related commands
 **/

// prettier-ignore
export const characterCommand: CommandObject = {
  [`${ESC}\x21` /* ESC ! */]: ["Select character printing mode", 1],
  [`${GS}\x21`  /* GS !  */]: ["Select character size", 1],
  [`${ESC}\x2d` /* ESC - */]: ["Turn underline mode on/off", 1],
  [`${ESC}\x45` /* ESC E */]: ["Turn emphasized mode on/off", 1],
  [`${GS}\x42`  /* GS B  */]: ["Turn white/black reverse printing mode on/off", 1],
  [`${ESC}\x56` /* ESC V */]: ["Turn 90 clockwise rotation mode on/off", 1],
  [`${ESC}\x49` /* ESC I */]: ["Turn italic print mode on/off", 1],
  [`${ESC}\x7b` /* ESC { */]: ["Turn upside-down printing mode on/off", 1],
  [`${ESC}\x3f` /* ESC ? */]: ["Cancel user-defined characters", 1],
  [`${FS}\x26`  /* FS &  */]: ["Enable ASIA character set"],
  [`${FS}\x57`  /* FS W  */]: ["Turn quadruple-size mode on/off for Kanji characters", 1],
  [`${ESC}\x74` /* ESC t */]: ["Select character code table", 1],
  [`${ESC}\x47` /* ESC G */]: ["Turn double-strike character on/off", 1],
};

