  [`${ESC}\x44`  /* ESC D  */]: ["Set horizontal tab position"], 
  [`${ESC}\x54`  /* ESC T  */]: ["Select print direction in page mode", 1], 
  [`${ESC}\x57`  /* ESC W  */]: ["Set printing area in page mode", 8], 
  [`${ESC}\x5c`  /* ESC \  */]: ["Set relative print position", 2], 
  [`${ESC}\x32`  /* ESC 2  */]: ["Set default line spacing (1/6 inch)"], 
