  [`${ESC}\x35`  /* ESC 5  */]: ["Set black-white reverse printing"], 
  [`${ESC}${SP}` /* ESC SP */]: ["Set character right space amount", 1], 
  [`${ESC}\x61`  /* ESC a  */]: ["Set position alignment/justification", 1], 
  [`${GS}\x4c`   /* GS L   */]: ["Set left margin", 2], 
