/**
 * Graphics/Image related commands
 *
 * Note: Raster bitmap image format that is being used here
 *       turns out to be exactly like a PBM file format but
 *       without the header data
 *
 *       refer to https://oceancolor.gsfc.nasa.gov/staff/norman/seawifs_image_cookbook/faux_shuttle/pbm.html
 *       for the PBM file format specification
 **/

// prettier-ignore
export const graphicCommand: CommandObject = {
  [`${ESC}\x2a`     /* ESC *  */]: ["Select bit image mode", 3],

  /**
   * TODO: need a confirmation for this one via real world example, 
   *       as we need to match with the printer output
   * 
   * Note: refer to this one https://twitter.com/angealbertini/status/535565222652948480/photo/1
   *       for the TGA specification
   */
  [`${ESC}\x2e`     /* ESC .  */]: ["Print Advanced Raster Graphics (presumably TGA)", 4], 

  [`${GS}\x2a`      /* GS *   */]: ["Define downloaded bit image", 2],
  [`${GS}\x2f`      /* GS /   */]: ["Print downloaded bit image", 1],
  [`${FS}\x71`      /* FS q   */]: ["Define NV bit image", 1],
  [`${FS}\x70`      /* FS p   */]: ["Print NV bit image", 2],

  /**
   * These two commands are basically the same
   */
  [`${GS}\x28\x4c`  /* GS ( L */]: ["Specify graphics data", 4], 
  [`${GS}\x38\x4c`  /* GS 8 L */]: ["Specify graphics data", 4], 

  [`${GS}\x76\x30`  /* GS v 0 */]: ["Print raster bit image", 5],
};

/**
 * QRcode/Barcode related commands
 **/

// prettier-ignore
export const barcodeCommand: CommandObject = {
  [`${GS}\x68`      /* GS h    */]: ["Set barcode height", 1],
  [`${GS}\x77`      /* GS w    */]: ["Set barcode width", 1], 
  [`${GS}\x48`      /* GS H    */]: ["Select HRI character print position", 1],
  [`${GS}\x66`      /* GS f    */]: ["Select HRI character font", 1],
  [`${GS}\x6b`      /* GS k    */]: ["Print barcode", 1], 
  [`${GS}\x28\x6b`  /* GS ( k  */]: ["Print barcode (PDF417, QR)", 4], 
};

/**
 * Miscellaneous commands
 *
 * Note: we will only parse these commands without
 *       doing anything with the data since it is
 *       not related with rendering at all
 **/

// prettier-ignore
export const miscCommand: CommandObject = {
  [`${GS}\x56`  /* GS V  */]: ["Select cut mode and cut paper", 1],
  [`${ESC}\x70` /* ESC p */]: ["Generate pulse", 3],
};
