import cloneDeep from 'just-clone';

import { CommandByteSequence, ParsedArgs, RuleTree } from '../types';
import * as Command from './commands';
import { convertToConsoleHexString } from './util';

export const checkChunkArgs = (command: CommandByteSequence, rawData: string[], parsedArgs?: ParsedArgs): ParsedArgs => {
  switch (command) {
    case `${Command.ESC}\x64`:
    case `${Command.FS}\x71`:
      if (parsedArgs && parsedArgs[0] === 0) {
        parsedArgs[0] = 1;
      }
      break;

    case `${Command.ESC}\x21`:
    case `${Command.GS}\x21`:
    case `${Command.ESC}\x2d`:
    case `${Command.ESC}\x45`:
    case `${Command.GS}\x42`:
    case `${Command.ESC}\x56`:
    case `${Command.ESC}\x49`:
    case `${Command.ESC}\x7b`:
    case `${Command.FS}\x57`:
    case `${Command.ESC}${Command.SP}`:
      if (!parsedArgs) {
        parsedArgs = [0];
      }
      break;

    case `${Command.ESC}\x3f`:
      if (parsedArgs && Number(parsedArgs[0]) > 32) {
        parsedArgs[0] = 32;
      }
      break;

    case `${Command.ESC}\x24`:
    case `${Command.GS}\x4c`:
      if (!parsedArgs) {
        parsedArgs = [0, 0];
      }

      if (parsedArgs.length === 1) {
        parsedArgs.push(0);
      }
      break;

    case `${Command.ESC}\x54`: {
      if (!parsedArgs) {
        parsedArgs = [0];
      }

      const n = Number(parsedArgs[0]);
      if (n > 3 && n < 48) {
        const middle = (48 - 3) / 2;
        parsedArgs[0] = Math.abs(n - middle) > middle ? 48 : 3;
        console.log('[Warning]: argument for print direction command is between 3 - 48, rounding it to the closest one');
      }

      if (n > 51) {
        parsedArgs[0] = 51;
        console.log('[Warning]: argument for print direction command is more than 51, rounding it to 51');
      }
      break;
    }

    case `${Command.ESC}\x57`: {
      if (parsedArgs) {
        const [dxL, dxH, dyL, dyH] = parsedArgs.slice(4).map((val) => Number(val));
        if (dxL + dxH * 256 === 0 || dyL + dyH * 256 === 0) {
          parsedArgs = [...parsedArgs.slice(0, 4), 64, 2, 64, 2];
          console.log('[Warning]: printing area width and/or height should not be set to 0, reverting back to default value (entire printable area)');
        }
      }
      break;
    }

    case `${Command.ESC}\x33`:
      if (!parsedArgs) {
        parsedArgs[0] = 54;
      }
      break;

    case `${Command.ESC}\x61`: {
      if (!parsedArgs) {
        parsedArgs = [0];
      }

      const n = Number(parsedArgs[0]);
      if (n > 2 && n < 48) {
        const middle = (48 - 2) / 2;
        parsedArgs[0] = Math.abs(n - middle) > middle ? 48 : 2;
        console.log('[Warning]: argument for justification command is between 2 - 48, rounding it to the closest one');
      }

      if (n > 50) {
        parsedArgs[0] = 50;
        console.log('[Warning]: argument for justification command is more than 50, rounding it to 50');
      }
      break;
    }

    case `${Command.GS}\x57`:
      if (!parsedArgs) {
        parsedArgs = [64, 2];
      }

      if (parsedArgs.length === 1) {
        parsedArgs.push(2);
      }
      break;

    case `${Command.ESC}\x2a`: {
      if (parsedArgs) {
        // TODO: Implement rule for
        //       `ESC * 1 nL nH` and `ESC * b m n`
        const [m, , nH] = parsedArgs.map((val) => Number(val));

        if (m > 1 && m < 32) {
          const middle = (32 - 1) / 2;
          parsedArgs[0] = Math.abs(m - middle) > middle ? 32 : 1;
          console.log('[Warning]: argument m for bit-image mode command is between 1 - 32, rounding it to the closest one');
        }

        if (m > 33) {
          parsedArgs[0] = 33;
          console.log('[Warning]: argument m for bit-image mode command is more than 33, rounding it to 33');
        }

        if (nH > 3) {
          parsedArgs[2] = 3;
          console.log('[Warning]: argument nH for bit-image mode command is more than 3, rounding it to 3');
        }
      }
      break;
    }

    case `${Command.ESC}\x2e`: {
      if (parsedArgs) {
        const [m, n] = parsedArgs.map((val) => Number(val));

        if (m > 72) {
          parsedArgs[0] = 72;
          console.log('[Warning]: argument m for TGA mode command is more than 72, rounding it to 72');
        }

        if (n > 72) {
          parsedArgs[1] = 72;
          console.log('[Warning]: argument n for TGA mode command is more than 72, rounding it to 72');
        }
      }
      break;
    }

    case `${Command.GS}\x2a`: {
      if (parsedArgs) {
        const [x, y] = parsedArgs.map((val) => Number(val));

        if (x > 80) {
          parsedArgs[0] = 80;
          console.log('[Warning]: argument x for define downloaded bit image mode command is more than 80, rounding it to 80');
        }

        if (y > 64) {
          parsedArgs[1] = 64;
          console.log('[Warning]: argument y for define downloaded bit image mode command is more than 64, rounding it to 64');
        }
      }
      break;
    }

    case `${Command.GS}\x2f`: {
      if (parsedArgs) {
        const m = Number(parsedArgs[0]);

        if (m > 3 && m < 48) {
          const middle = (48 - 3) / 2;
          parsedArgs[0] = Math.abs(m - middle) > middle ? 48 : 3;
          console.log('[Warning]: argument m for print downloaded bit image command is between 3 - 48, rounding it to the closest one');
        }

        if (m > 51) {
          parsedArgs[0] = 51;
          console.log('[Warning]: argument m for print downloaded bit image command is more than 51, rounding it to 51');
        }
      }
      break;
    }

    case `${Command.FS}\x70`: {
      if (parsedArgs) {
        const [n, m] = parsedArgs.map((val) => Number(val));

        if (n === 0) {
          parsedArgs[0] = 1;
        }

        if (m > 3 && m < 48) {
          const middle = (48 - 3) / 2;
          parsedArgs[1] = Math.abs(m - middle) > middle ? 48 : 3;
          console.log('[Warning]: argument m for print NV bit image command is between 3 - 48, rounding it to the closest one');
        }

        if (m > 51) {
          parsedArgs[1] = 51;
          console.log('[Warning]: argument m for print NV bit image command is more than 51, rounding it to 51');
        }
      }
      break;
    }

    case `${Command.GS}\x28\x4c`:
    case `${Command.GS}\x38\x4c`: {
      if (parsedArgs) {
        const [pL, pH, m, fn] = parsedArgs.map((val) => Number(val));

        switch (fn) {
          case 0:
          case 48:
            break;

          /**
           * Print the graphic data in the print buffer
           */
          case 2:
          case 50:
            if (pL !== 2) {
              parsedArgs[0] = 2;
              console.log('[Warning]: argument pL for print graphic data command is not 2, changing it to 2');
            }

            if (pH !== 0) {
              parsedArgs[1] = 0;
              console.log('[Warning]: argument pH for print graphic data command is not 0, changing it to 0');
            }

            if (m !== 48) {
              parsedArgs[2] = 48;
              console.log('[Warning]: argument m for print graphic data command is not 48, changing it to 48');
            }
            break;

          case 3:
          case 51:
            break;

          case 64:
            break;

          case 65:
            break;

          case 66:
            break;

          case 67:
            break;

          case 69:
            break;

          /**
           * Stores the raster graphic data in the print buffer memory
           */
          case 112: {
            parsedArgs.push(...rawData.slice(4, 12).map((val) => val.charCodeAt(0)));
            const [a, bx, by, c, , xH, , yH] = parsedArgs.slice(4) as number[];

            const dataSize = pL + pH * 256;
            if (dataSize > 65535 || dataSize < 11) {
              throw new Error('Data size for storing graphic data command is not in range of 11 - 65535');
            }

            if (m !== 48) {
              parsedArgs[2] = 48;
              console.log('[Warning]: argument m for storing graphic data command is not 48, changing it to 48');
            }

            if (a !== 48) {
              parsedArgs[4] = 48;
              console.log('[Warning]: argument a for storing graphic data command is not 48, changing it to 48');
            }

            if (bx > 2) {
              parsedArgs[5] = 2;
              console.log('[Warning]: argument bx for storing graphic data command is more than 2, rounding it to 2');
            }

            if (bx < 1) {
              parsedArgs[5] = 1;
              console.log('[Warning]: argument bx for storing graphic data command is less than 1, rounding it to 1');
            }

            if (by > 2) {
              parsedArgs[6] = 2;
              console.log('[Warning]: argument by for storing graphic data command is more than 2, rounding it to 2');
            }

            if (by < 1) {
              parsedArgs[6] = 1;
              console.log('[Warning]: argument by for storing graphic data command is less than 1, rounding it to 1');
            }

            if (c !== 49) {
              parsedArgs[7] = 49;
              console.log('[Warning]: argument c for storing graphic data command is not 49, changing it to 49');
            }

            if (xH > 3) {
              parsedArgs[9] = 3;
              console.log('[Warning]: argument xH for storing graphic data command is more than 3, rounding it to 3');
            }

            if (by === 1 && yH > 5) {
              parsedArgs[11] = 5;
              console.log('[Warning]: argument yH for storing graphic data command is more than 5, rounding it to 5');
            }

            if (by === 2 && yH > 2) {
              parsedArgs[11] = 2;
              console.log('[Warning]: argument yH for storing graphic data command is more than 2, rounding it to 2');
            }
            break;
          }

          default:
            break;
        }
      }
      break;
    }

    case `${Command.GS}\x76\x30`: {
      if (parsedArgs) {
        const [m, , , , yH] = parsedArgs.map((val) => Number(val));

        if (m > 3 && m < 48) {
          const middle = (48 - 3) / 2;
          parsedArgs[0] = Math.abs(m - middle) > middle ? 48 : 3;
          console.log('[Warning]: argument m for print raster bit image command is between 3 - 48, rounding it to the closest one');
        }

        if (m > 51) {
          parsedArgs[0] = 51;
          console.log('[Warning]: argument m for print raster bit image command is more than 51, rounding it to 51');
        }

        if (yH > 8) {
          parsedArgs[4] = 8;
          console.log('[Warning]: argument yH for print raster bit image command is more than 8, rounding it to 8');
        }
      }
      break;
    }

    case `${Command.GS}\x68`:
      if (!parsedArgs) {
        parsedArgs = [216];
      }

      if (parsedArgs[0] === 0) {
        parsedArgs[0] = 1;
      }
      break;

    case `${Command.GS}\x77`: {
      if (!parsedArgs) {
        parsedArgs = [3];
      }

      const n = Number(parsedArgs[0]);
      if (n > 6) {
        parsedArgs[0] = 6;
        console.log('[Warning]: argument n for barcode width command is more than 6, rounding it to 6');
      }

      if (n < 2) {
        parsedArgs[0] = 2;
        console.log('[Warning]: argument n for barcode width command is less than 2, rounding it to 2');
      }
      break;
    }

    case `${Command.GS}\x48`: {
      if (!parsedArgs) {
        parsedArgs = [0];
      }

      const n = Number(parsedArgs[0]);
      if (n > 3 && n < 48) {
        const middle = (48 - 3) / 2;
        parsedArgs[0] = Math.abs(n - middle) > middle ? 48 : 3;
        console.log('[Warning]: argument n for print position of HRI char command is between 3 - 48, rounding it to the closest one');
      }

      if (n > 51) {
        parsedArgs[0] = 51;
        console.log('[Warning]: argument n for print position of HRI char command is more than 51, rounding it to 51');
      }
      break;
    }

    case `${Command.GS}\x66`: {
      if (!parsedArgs) {
        parsedArgs = [0];
      }

      const n = Number(parsedArgs[0]);
      if (n > 1 && n < 48) {
        const middle = (48 - 1) / 2;
        parsedArgs[0] = Math.abs(n - middle) > middle ? 48 : 1;
        console.log('[Warning]: argument n for font of HRI char command is between 1 - 48, rounding it to the closest one');
      }

      if (n > 49) {
        parsedArgs[0] = 49;
        console.log('[Warning]: argument n for font of HRI char command is more than 49, rounding it to 49');
      }
      break;
    }

    case `${Command.GS}\x6b`: {
      if (parsedArgs) {
        const m = Number(parsedArgs[0]);

        if (m > 16 && m < 65) {
          const middle = (65 - 16) / 2;
          parsedArgs[0] = Math.abs(m - middle) > middle ? 65 : 16;
          console.log('[Warning]: argument n for print barcode command is between 16 - 65, rounding it to the closest one');
        }

        if (m > 78) {
          parsedArgs[0] = 78;
          console.log('[Warning]: argument m for print barcode command is more than 78, rounding it to 78');
        }
      }
      break;
    }

    case `${Command.GS}\x28\x6b`: {
      if (parsedArgs) {
        const [, , cn, fn] = parsedArgs.map((val) => Number(val));

        if (cn < 48) {
          parsedArgs[2] = 48;
          console.log('[Warning]: argument cn for print barcode (PDF417, QR) command is less than 48, rounding it to 48');
        }

        if (cn > 52) {
          parsedArgs[2] = 52;
          console.log('[Warning]: argument cn for print barcode (PDF417, QR) command is more than 52, rounding it to 52');
        }

        if (fn < 65) {
          parsedArgs[3] = 65;
          console.log('[Warning]: argument fn for print barcode (PDF417, QR) command is less than 65, rounding it to 65');
        }

        if (fn > 72 && fn < 80) {
          const middle = (80 - 72) / 2;
          parsedArgs[3] = Math.abs(fn - middle) > middle ? 80 : 72;
          console.log('[Warning]: argument fn for print barcode (PDF417, QR) command is between 72 - 80, rounding it to the closest one');
        }

        if (fn > 82) {
          parsedArgs[3] = 82;
          console.log('[Warning]: argument fn for print barcode (PDF417, QR) command is more than 82, rounding it to 82');
        }
      }
      break;
    }
  }

  if (!parsedArgs) {
    throw new Error(`Required argument for [${convertToConsoleHexString(command)}] command is missing`);
  }

  return parsedArgs;
};

const mergeRuleTree = (commandTree: RuleTree, newNode: RuleTree) => {
  if (newNode !== null && typeof newNode === 'object') {
    for (const [byte, value] of Object.entries(newNode)) {
      let matchFound = false;

      if (commandTree[byte]) {
        mergeRuleTree(commandTree[byte], value);
        matchFound = true;
        break;
      }

      if (!matchFound) {
        Object.assign(commandTree, newNode);
      }
    }
  }
};

export const findRuleTree = (commandTree: RuleTree, byteSequenceToFind: string[]): RuleTree => {
  let clonedCommandTree = cloneDeep(commandTree);

  for (const byte of byteSequenceToFind) {
    if (byte in clonedCommandTree) {
      const node = cloneDeep(clonedCommandTree[byte]);

      clonedCommandTree = {};
      clonedCommandTree = node;
    } else {
      clonedCommandTree = {};
      break;
    }
  }

  return clonedCommandTree;
};

export const generateRuleTree = (): RuleTree => {
  const combinedCommands = {
    ...Command.basicCommand,
    ...Command.characterCommand,
    ...Command.layoutCommand,
    ...Command.graphicCommand,
    ...Command.barcodeCommand,
    ...Command.miscCommand,
  };

  const fullCommandsList: RuleTree = {};
  for (const [command, description] of Object.entries(combinedCommands)) {
    const commandList = command.split('').reverse();

    let commandTree: RuleTree = {
      [commandList.shift() as string]: {
        content: description,
      },
    };

    for (const byte of commandList) {
      const tempCommandTree = cloneDeep(commandTree);

      commandTree = {};
      commandTree[byte] = tempCommandTree;
    }

    mergeRuleTree(fullCommandsList, commandTree);
  }

  return fullCommandsList;
};
