<template>
  <ModalBackdrop :show="show" margin="m-3" padding="p-6">
    <div class="flex flex-col gap-y-3" data-cy="dob-gender-prompt">
      <div>
        <span class="font-poppins font-semibold text-xl">
          {{ $t('DataPrompt.Title') }}
        </span>
      </div>
      <div>
        {{ $t('DataPrompt.Body') }}
      </div>
      <div class="flex flex-col gap-y-2">
        <div class="flex justify-between">
          <span class="font-poppins font-medium text-xl">
            {{ $t('DataPrompt.Gender') }}
          </span>
          <InputSelect selectPlaceholder :value="options.gender()" v-model="userRef.gender" :dataCy="'gender_prompt_select'" class="cursor-pointer" />
        </div>
        <div class="flex justify-between">
          <span class="font-poppins font-medium text-xl">
            {{ $t('DataPrompt.YOB') }}
          </span>
          <button @click="openDateModal" data-cy="yob_prompt_select">
            <span>{{ userRef.yob || $t('DataPrompt.Select') }}</span>
            <span>
              <font-awesome-icon icon="fa-solid fa-chevron-right" size="xs" />
            </span>
          </button>
        </div>
      </div>
      <div class="flex flex-col gap-y-2">
        <button
          class="bg-primary-blue hover:enabled:grayscale-[30%] rounded-lg disabled:cursor-not-allowed disabled:bg-primary-blue/50 p-2"
          :disabled="disabledButton"
          @click="submitData"
          data-cy="dob-gender-prompt-submit"
        >
          <span class="text-white text-xl">
            {{ $t('DataPrompt.Submit') }}
          </span>
        </button>
        <button class="rounded-lg hover:bg-gray-100 hover:text-primary-blue p-2" @click="emit('closeModal')" data-cy="dob-gender-prompt-later">
          <span class="text-grey-100 text-xl">
            {{ $t('DataPrompt.Later') }}
          </span>
        </button>
      </div>
    </div>
  </ModalBackdrop>

  <ModalBackdrop @on-close="closeDateModal" :show="dateModal" :margin="''" :padding="''">
    <Datepicker
      data-cy="yob_prompt_input"
      inline
      autoApply
      yearPicker
      :enable-time-picker="false"
      ref="datepicker"
      v-model="defaultYear"
      @update:model-value="handleDate"
      :maxDate="new Date()"
    />
  </ModalBackdrop>
</template>

<script lang="ts" setup>
import { useUpdateUserInfo } from '@/api/user';
import { useAlertStore } from '@/stores/alert';
import { useModalErrorMsg } from '@/utils/hooks/useErrorHandling';
import { UserUpdateInput } from '@pi-xcels/graphql/types';
import { i18n } from '@pi-xcels/utils';
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';
import { computed, reactive, readonly, ref, watch, watchEffect } from 'vue';
import { useI18n } from 'vue-i18n';

const props = defineProps<{
  show: boolean;
  userDataProp: any;
}>();

const emit = defineEmits(['closeModal']);
const { t } = useI18n();
const alertState = useAlertStore();

const options = reactive({
  gender() {
    return [
      {
        value: 'Male',
        label: i18n.global.t('Settings.Male'),
      },
      {
        value: 'Female',
        label: i18n.global.t('Settings.Female'),
      },
      {
        value: 'Other',
        label: i18n.global.t('Settings.Other'),
      },
    ];
  },
});
const userRef: UserUpdateInput = reactive({
  gender: '',
  yob: 0,
});

const userData: any = computed(() => {
  return {
    gender: props?.userDataProp?.gender ?? '',
    yob: props?.userDataProp?.yob ?? '',
  };
});

const disabledButton = ref(true);
const dateModal = ref(false);
const datepicker = ref(null);
const defaultYear = ref();

defineExpose({
  userData,
});

const openDateModal = () => {
  dateModal.value = true;
};

const closeDateModal = () => {
  dateModal.value = false;
};

const handleDate = (modelData: any) => {
  dateModal.value = false;
  userRef.yob = modelData;
};

const { mutateAsync: userUpdate, isSuccess, error: userUpdateError } = useUpdateUserInfo();
watch(isSuccess, (isSuccess) => {
  if (!isSuccess) return;
  emit('closeModal');
});
useModalErrorMsg(
  userUpdateError,
  readonly(
    ref({
      id: 'update-user-info-error',
      title: t('Error.AccountUpdateFailed.Title', { provider: 'Facebook' }),
      body: t('Error.AccountUpdateFailed.Body'),
      btnTxt: t('Error.BtnTextOK'),
      btnAction() {
        alertState.removeAlertMessage('update-user-info-error');
      },
    })
  )
);

const submitData = () => {
  userUpdate({ variables: { data: { ...userRef } } });
};

watchEffect(() => {
  Object.assign(userRef, userData.value);
});

watchEffect(() => {
  if (userRef.gender && userRef.yob) {
    disabledButton.value = false;
  }

  defaultYear.value = userRef.yob ?? new Date().getFullYear() - 20;
});
</script>
