import * as Command from '../parser/commands';
import { ChunkData } from '../types';
import { PrinterContext } from './printer';
import { escposCodepagesMapping, parseTextBytes } from './text';

/**
 * General AST walker to set printerContext correctly without messing around
 * with rendering and other things (including the analyzer)
 *
 * Put all escpos command that will **only** alter the printerContext (and
 * nothing else) here
 */
export function* walkAST(ast: ChunkData[], printerContext: PrinterContext): Generator<[number, ChunkData, string, boolean]> {
  for (const [index, node] of ast.entries()) {
    const textData = parseTextBytes(node.extractedData?.text || [], printerContext.getTextState().codepage);
    let commandHandled = true;

    switch (node.command) {
      case `${Command.ESC}\x40`:
        printerContext.resetStates();
        break;

      case `${Command.ESC}\x45`: {
        const [n] = node.parsedArgs.map((val) => val.toString(2));
        printerContext.setBold(Boolean(n[n.length - 1]));
        break;
      }

      case `${Command.ESC}\x33`: {
        const [n] = node.parsedArgs as number[];
        printerContext.setLineSpacing(n);
        break;
      }

      case `${Command.ESC}\x21`: {
        const [n] = node.parsedArgs as number[];
        // Get each bits for each related feature
        const [isBold, isDoubleHeight, isDoubleWidth, isUnderline] = [3, 4, 5, 7].map((val) => Boolean((n >> val) & 0x1));
        printerContext.setBold(isBold);
        printerContext.setUnderline(isUnderline);
        printerContext.setWidth(isDoubleWidth ? 2 : 1);
        printerContext.setHeight(isDoubleHeight ? 2 : 1);
        break;
      }

      case `${Command.GS}\x21`: {
        const [n] = node.parsedArgs as number[];
        // Get lower 3 bits and upper 3 bits
        const [height, width] = [0, 4].map((val) => ((n >> val) & 0x7) + 1);
        printerContext.setWidth(width);
        printerContext.setHeight(height);
        break;
      }

      case `${Command.ESC}\x61`: {
        const [n] = node.parsedArgs as number[];
        switch (n) {
          case 0:
          case 48:
            printerContext.setPosition('left');
            break;

          case 1:
          case 49:
            printerContext.setPosition('center');
            break;

          case 2:
          case 50:
            printerContext.setPosition('right');
            break;
        }
        break;
      }

      case `${Command.ESC}\x74`: {
        const [n] = node.parsedArgs as number[];
        const codepagesMapping = escposCodepagesMapping();

        if (!Object.keys(codepagesMapping).includes(String(n))) {
          console.log(`[Warning]: Codepage ${n} is not supported yet`);
          break;
        }

        printerContext.setCodepage(codepagesMapping[n]);
        break;
      }

      default:
        commandHandled = false;
        break;
    }

    yield [index, node, textData, commandHandled];
  }
}
