<template>
  <div v-html="renderedReceipt" id="rendered-receipt"></div>

  <!-- Dummy element to generate barcode via JSBarcode -->
  <svg ref="barcodeGen" id="generate-barcode" class="hidden"></svg>
</template>

<style scoped lang="scss">
#rendered-receipt:deep(.ripple) {
  position: relative;
  overflow: hidden;
}

#rendered-receipt:deep(.ripple .rippleEffect) {
  position: absolute;
  background: #c6c6c6;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  pointer-events: none;
  animation: ripples 0.6s linear infinite;
  -webkit-animation: ripples 0.6s linear infinite;
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -ms-border-radius: 100%;
  -o-border-radius: 100%;
}

@keyframes ripples {
  0% {
    width: 0px;
    height: 0px;
    opacity: 0.5;
  }
  100% {
    width: 500px;
    height: 500px;
    opacity: 0;
  }
}
</style>

<script setup lang="ts">
import { sanitize } from 'isomorphic-dompurify';
import Mustache from 'mustache';
import facebookIcon from '@/assets/images/icons/fb-icon.png';
import instagramIcon from '@/assets/images/icons/instagram-icon.png';
import linkedinIcon from '@/assets/images/icons/linkedin-logo.svg';
import twitterIcon from '@/assets/images/icons/twitter-icon.png';
import webIcon from '@/assets/images/icons/web-logo.svg';
import whatsappIcon from '@/assets/images/whatsapp.svg';
import foodIcon from '@/assets/images/icons/food.svg';
import homecareIcon from '@/assets/images/icons/homecare.svg';
import beverageIcon from '@/assets/images/icons/beverage.svg';
import bakeryIcon from '@/assets/images/icons/bakery.svg';
import personalCareIcon from '@/assets/images/icons/personalcare.svg';
import promoIcon from '@/assets/images/icons/promo.svg';
import nonFoodIcon from '@/assets/images/icons/non-food.svg';
import wingsIcon from '@/assets/images/icons/wings.png';
import coffeContentImg01 from '@/assets/images/Coffee-Content-01.png';
import coffeContentImg02 from '@/assets/images/Coffee-Content-02.png';
import coffeContentImg03 from '@/assets/images/Coffee-Content-03.png';
import coffeContentImg05 from '@/assets/images/Coffee-Content-05.png';
import coffeContentImg06 from '@/assets/images/Coffee-Content-06.png';
import JsBarcode from 'jsbarcode';
import { Buffer } from 'buffer';
import { getCompanyLogoURL } from '@/utils/receipt';

import { formatPrice, i18n } from '@pi-xcels/utils';
import { nextTick, onMounted, ref, watchEffect } from 'vue';

const props = defineProps(['data', 'companyData', 'isPreSettlementReceipt', 'template', 'sandboxMode']);
const barcodeGen = ref<HTMLElement | null>(null);
const renderedReceipt = ref();

onMounted(() => {
  // This has to be inside onMounted for the JSBarcode to work properly
  watchEffect(async () => {
    renderedReceipt.value = sanitize(
      Mustache.render(props.template, {
        data: props.data,
        sandboxMode: props.sandboxMode,
        companyData: props.companyData,
        isPreSettlementReceipt: props.isPreSettlementReceipt,
        generateIcon() {
          return function (text: string, render: Function) {
            let icon = null;

            switch (render(text.trim())) {
              case 'facebookIcon':
                icon = facebookIcon;
                break;

              case 'instagramIcon':
                icon = instagramIcon;
                break;

              case 'linkedinIcon':
                icon = linkedinIcon;
                break;

              case 'twitterIcon':
                icon = twitterIcon;
                break;

              case 'webIcon':
                icon = webIcon;
                break;

              case 'whatsappIcon':
                icon = whatsappIcon;
                break;

              case 'foodIcon':
                icon = foodIcon;
                break;

              case 'homeCareIcon':
                icon = homecareIcon;
                break;

              case 'beverageIcon':
                icon = beverageIcon;
                break;

              case 'bakeryIcon':
                icon = bakeryIcon;
                break;

              case 'personalCareIcon':
                icon = personalCareIcon;
                break;

              case 'promotionIcon':
                icon = promoIcon;
                break;

              case 'nonFoodIcon':
                icon = nonFoodIcon;
                break;

              case 'noodlesIcon':
                icon = wingsIcon;
                break;

              case 'coffeContentImg01':
                icon = coffeContentImg01;
                break;

              case 'coffeContentImg02':
                icon = coffeContentImg02;
                break;

              case 'coffeContentImg03':
                icon = coffeContentImg03;
                break;

              case 'coffeContentImg05':
                icon = coffeContentImg05;
                break;

              case 'coffeContentImg06':
                icon = coffeContentImg06;
                break;
            }

            return icon;
          };
        },
        translate() {
          return function (text: string, render: Function) {
            const { t } = i18n.global;
            return t(render(text.trim()));
          };
        },
        formatPrice() {
          return function (text: string, render: Function) {
            const price = Number(render(text.trim()));
            const locale = props.companyData?.locale;
            const currencyCode = props.data?.CurrencyCode || props.companyData?.currencyCode;

            return formatPrice(price, locale, currencyCode);
          };
        },
        /**
         * Generate barcode using wrapper by tricking the lambda support
         * for mustache such that the string inside the lambda tag will
         * be treated as a json string so it could be parsed as json object
         * inside this function, so this have to be used this way for example:
         *
         * {{#generateBarcode}}
         *  {
         *    "value": "{{ data.ReceiptNo }}"
         *    "format": "CODE128"
         *    "lineColor": "#000"
         *    "width": "2"
         *    "height": "41"
         *    "displayValue": "false"
         *  }
         * {{/generateBarcode}}
         *
         * The `data.ReceiptNo` in the example will be rendered first using
         * the given builtin render function and then later the whole block
         * of text inside the lambda tag will be parsed as a json object.
         *
         * And this lambda tag will later be converted as an html `img` tag
         * as the result.
         */
        generateBarcode() {
          return function (text: string, render: Function) {
            const configurations = JSON.parse(render(text.trim()));

            const settings: Record<string, any> = {
              format: configurations.format,
              width: configurations.width,
              height: configurations.height,
              displayValue: configurations.displayValue,
              text: configurations.text,
              fontOptions: configurations.fontOptions,
              font: configurations.font,
              textAlign: configurations.textAlign,
              textPosition: configurations.textPosition,
              textMargin: configurations.textMargin,
              fontSize: configurations.fontSize,
              background: configurations.background,
              lineColor: configurations.lineColor,
              margin: configurations.margin,
              marginTop: configurations.marginTop,
              marginBottom: configurations.marginBottom,
              marginLeft: configurations.marginLeft,
              marginRight: configurations.marginRight,
              flat: configurations.flat,
              ean128: configurations.ean128,
            };

            // Remove all undefined value from settings
            Object.keys(settings).forEach((key: string) => (settings[key] === undefined ? delete settings[key] : {}));

            // Generate the barcode in the dummy element
            JsBarcode(`#generate-barcode`, configurations.value, settings);

            // Grab the resulting base64 svg data and remove the element afterward
            const base64SvgData = Buffer.from(new XMLSerializer().serializeToString(barcodeGen.value!)).toString('base64');

            return `<img src="data:image/svg+xml;base64,${base64SvgData}" class="${configurations.class}"/>`;
          };
        },
        generateCompanyLogo() {
          return function () {
            return `<img src="${getCompanyLogoURL(props.companyData.logos)}" class="w-auto h-full" alt="Company logo" />`;
          };
        },
      }),
      {
        ADD_TAGS: ['iframe'],
        ADD_ATTR: ['allow', 'allowfullscreen', 'frameborder'],
      }
    );

    await nextTick();

    /**
     * This is the glue code to enable on clicking event in the whatsapp support button
     * indicated by `whatsapp-support-button` id, which then will make the whatsapp receipt
     * prompt visible.
     *
     * This needs to be written this way to avoid all kinds of security vulnerability, and
     * its also better this way to keep the mustache template as language agnostic as possible,
     * such that later in the future its possible to move all the html generation inside this
     * renderer instead of staying in the mustache template itself.
     */
    const whatsappSupportButtonEl = document.getElementById('whatsapp-support-button');
    if (whatsappSupportButtonEl) {
      whatsappSupportButtonEl.addEventListener('click', function () {
        const whatsappReceiptPromptEl = document.getElementById('whatsapp-receipt-prompt');

        if (whatsappReceiptPromptEl) {
          whatsappReceiptPromptEl.classList.add('visible');
          whatsappReceiptPromptEl.classList.remove('hidden');
        }
      });
    }

    // Add ripple effect
    (document.querySelectorAll('.ripple') as NodeListOf<HTMLElement>).forEach(function (ripple) {
      ripple.onclick = function (e: any) {
        const rect = ripple.getBoundingClientRect();
        const x = e.clientX - rect.left;
        const y = e.clientY - rect.top;

        const rippleEffect = document.createElement('span');
        rippleEffect.classList.add('rippleEffect');

        rippleEffect.style.left = `${x}px`;
        rippleEffect.style.top = `${y}px`;

        ripple.appendChild(rippleEffect);
        setTimeout(() => {
          rippleEffect.remove();
        }, 600);
      };
    });
  });
});
</script>
