import { sanitize } from 'isomorphic-dompurify';

import { ParserManager } from '../parser';
import * as Command from '../parser/commands';
import { convertToConsoleHexString } from '../parser/util';
import { ChunkData, CommandByteSequence, PixelData, RenderResult, SvgData } from '../types';
import { getPrefixTransformStyleString } from './css';
import * as Html from './html';
import { PrinterContext } from './printer';
import { walkAST } from './util';

const renderImage = (extractedImageData: PixelData[][], renderAs: RenderResult, printerContext: PrinterContext, additionalStyles: { [key: string]: string } = {}) => {
  if (renderAs === 'html')
    return Html.wrapInDiv(Html.svgImage(extractedImageData, printerContext), {
      display: 'flex',
      ...additionalStyles,
    });
};

const renderChunkImage = (extractedImageData: PixelData[][], renderAs: RenderResult) => {
  if (renderAs === 'html') return Html.svgChunkImage(extractedImageData);
};

const compileChunkImage = (chunkImages: SvgData[], renderAs: RenderResult, printerContext: PrinterContext) => {
  if (renderAs === 'html')
    return Html.wrapInDiv(Html.compileSvgChunkImage(chunkImages, printerContext), {
      display: 'flex',
      height: `${Number(chunkImages[0].styles.height) * (chunkImages.length - 1)}px`,
    });
};

const print = (extractedTextData: string, renderAs: RenderResult, printerContext: PrinterContext) => {
  if (renderAs === 'html') return Html.text(extractedTextData, printerContext);
};

const printAndLineFeed = (extractedTextData: string, renderAs: RenderResult, printerContext: PrinterContext, nLines = 1) => {
  if (renderAs === 'html') return Html.text(extractedTextData, printerContext).concat(lineFeedNtimes(nLines, renderAs));
};

const lineFeedNtimes = (nLines: number, renderAs: RenderResult) => {
  if (renderAs === 'html') {
    let lineBreaks = '';
    for (let i = 0; i < nLines; i++) {
      lineBreaks += Html.lineBreak();
    }
    return lineBreaks;
  }
};

const findBeforeResetCommand = (commandToSearch: CommandByteSequence, ast: ChunkData[]) => {
  for (const node of ast) {
    if (node.command === `${Command.ESC}\x40`) return false;
    if (node.command === commandToSearch) return true;
  }

  return false;
};

export const render = (binaryReceiptData: string[] | string, renderAs: RenderResult = 'html') => {
  if (renderAs === 'png') {
    throw new Error('PNG renderer is not implemented yet');
  }

  const printerContext = new PrinterContext();
  const ast = new ParserManager().parseRawData(binaryReceiptData).ast;

  const resultStrings: string[] = [];
  for (const [index, node, textData, commandHandled] of walkAST(ast, printerContext)) {
    switch (node.command) {
      case `${Command.ESC}\x2a`:
        // Add up all image as each chunk (for each column) and then compile
        // them together as one in the last `ESC *` command
        if (findBeforeResetCommand(`${Command.ESC}\x2a`, ast.slice(index + 1))) {
          printerContext.setColumnImageBuffer([...printerContext.getImageColumnBuffer(), renderChunkImage(node.extractedData.image, renderAs)]);
        } else {
          resultStrings.push(
            compileChunkImage([...printerContext.getImageColumnBuffer(), renderChunkImage(node.extractedData.image, renderAs)], renderAs, printerContext)
          );
        }
        break;

      case `${Command.GS}\x28\x4c`:
      case `${Command.GS}\x38\x4c`: {
        const [, , , fn] = node.parsedArgs as number[];

        switch (fn) {
          case 2:
          case 50:
            if (printerContext.getImageBuffer() !== '') {
              resultStrings.push(printerContext.getImageBuffer());
            } else {
              console.log(`[Warning]: Image buffer is empty, make sure you run the function 112 of ${convertToConsoleHexString(node.command)} command first`);
            }
            break;

          case 112:
            printerContext.setImageBuffer(renderImage(node.extractedData.image, renderAs, printerContext));
            break;
        }
        break;
      }

      case `${Command.GS}\x76\x30`:
        resultStrings.push(
          renderImage(node.extractedData.image, renderAs, printerContext, {
            'transform-origin': 'left',
            ...(getPrefixTransformStyleString(`scale(0.75)`, 'object') as object),
          })
        );
        break;

      case `${Command.ESC}\x64`: {
        if (ast[index - 1].command === `${Command.ESC}\x2a`) {
          // If previous command is `ESC *` then ignore the linefeed
          break;
        }

        const [nLines] = node.parsedArgs as number[];
        resultStrings.push(printAndLineFeed(textData, renderAs, printerContext, nLines));
        break;
      }

      case `${Command.LF}`:
      case `${Command.CR}`:
        if (ast[index - 1].command === `${Command.ESC}\x2a`) {
          // If previous command is `ESC *` then ignore the linefeed
          break;
        }

        resultStrings.push(printAndLineFeed(textData, renderAs, printerContext));
        break;

      default:
        if (node.command === '' && textData.length > 0) {
          resultStrings.push(print(textData, renderAs, printerContext));
        } else if (!commandHandled) {
          console.log(`[Warning]: [${convertToConsoleHexString(node.command)}] command have no renderer function`);
        }
        break;
    }
  }

  const result = (() => {
    if (renderAs === 'html') {
      return sanitize(
        `
        <div style="
          font-family: monospace;
          display: inline-block;
          white-space: pre;
          margin-left: auto;
          margin-right: auto;
          max-width: 100%;
        ">${resultStrings.join('')}</div>`.trim()
      );
    }
  })();

  return {
    result,
    ast,
  };
};
