<template>
  <div class="h-full w-full" id="receipt-detail" data-cy="ESCPOSReceipt">
    <div class="w-full flex mb-5" v-if="!dataHaveImages">
      <img :src="getCompanyLogoURL(companyData.logos)" alt="Company logo" class="w-auto h-full mx-auto" />
    </div>
    <div id="escpos-receipt-html" class="h-auto w-full flex-auto flex overflow-x-hidden relative" v-html="renderedReceipt.result"></div>
  </div>
</template>

<style scoped lang="scss">
#escpos-receipt-html:deep(span) {
  font-size: 9px;

  @media screen and (min-width: 320px) {
    font-size: calc(9px + 5 * ((100vw - 320px) / (448 - 320)));
  }

  @media screen and (min-width: 448px) {
    font-size: 14px;
  }
}
</style>

<script setup lang="ts">
import { render, convertBufferToStringBytes } from '@pi-xcels/escpos';
import { getCompanyLogoURL } from '@/utils/receipt';
import { computed } from 'vue';
import { Buffer } from 'buffer';

const props = defineProps(['data', 'companyData']);

const dataHaveImages = computed(() => {
  for (const node of renderedReceipt.value.ast) {
    if (node.extractedData?.image) {
      return true;
    }
  }

  return false;
});

const renderedReceipt = computed(() => {
  return render(convertBufferToStringBytes(Buffer.from(props.data, 'base64')))!;
});
</script>
