import { Position, SvgData } from '../types';

export class PrinterContext {
  #textState: {
    bold: boolean;
    italic: boolean;
    underline: boolean;
    width: number;
    height: number;
    linespacing: number;
    position: Position;
    codepage: string;
  };
  #imageBuffer: string;
  #columnImageBuffer: SvgData[];

  constructor() {
    // Initialize and reset all states
    this.resetStates();
  }

  setBold(isEnable: boolean) {
    this.#textState.bold = isEnable;
  }

  setItalic(isEnable: boolean) {
    this.#textState.italic = isEnable;
  }

  setUnderline(isEnable: boolean) {
    this.#textState.underline = isEnable;
  }

  setWidth(size: number) {
    this.#textState.width = size;
  }

  setHeight(size: number) {
    this.#textState.height = size;
  }

  setLineSpacing(size: number) {
    this.#textState.linespacing = size;
  }

  setPosition(position: Position) {
    this.#textState.position = position;
  }

  setCodepage(codepage: string) {
    this.#textState.codepage = codepage;
  }

  setImageBuffer(buffer: string) {
    this.#imageBuffer = buffer;
  }

  setColumnImageBuffer(buffer: SvgData[]) {
    this.#columnImageBuffer = buffer;
  }

  getTextState() {
    return this.#textState;
  }

  getImageBuffer() {
    return this.#imageBuffer;
  }

  getImageColumnBuffer() {
    return this.#columnImageBuffer;
  }

  resetStates() {
    this.#textState = {
      bold: false,
      italic: false,
      underline: false,
      width: 1,
      height: 1,
      linespacing: 54,
      position: 'left',
      codepage: '',
    };

    this.#imageBuffer = '';
    this.#columnImageBuffer = [];
  }
}
