import { useAuthManager } from '@/utils/auth';
import { useFullScreenErrorMsg, useToastErrorMsg } from '@/utils/hooks/useErrorHandling';
import { useLocalStorageManager } from '@/utils/localstorage';
import { useGetCurrentReceipt, useSubscribeToReceipt } from '@/utils/receipt';
import normalizeQueryValue from '@/utils/route';
import errorIcon from '@pi-xcels/ui-components/assets/images/error.svg';
import clone from 'just-clone';
import objectHash from 'object-hash';
import { Ref, computed, readonly, ref, unref, watch, watchEffect } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute } from 'vue-router';

export function useWaitingRoomCurrentReceiptHandler({
  transactionRef,
  isSettingDemoTransactionRef,
  isCreateTransactionSuccess,
}: {
  transactionRef: Readonly<Ref<string>>;
  isSettingDemoTransactionRef: Readonly<Ref<boolean>>;
  isCreateTransactionSuccess: Readonly<Ref<boolean>>;
}) {
  const { isAuthenticated, isAnonymous } = useAuthManager();
  const LocalStorageManager = useLocalStorageManager();
  const route = useRoute();
  const mode = computed(() => normalizeQueryValue(route?.query.mode));
  const isShareMode = computed(() => mode.value === 'share');
  const isViewMode = computed(() => mode.value === 'view');
  const { t } = useI18n();

  const { data: latestReceiptSubscription, error: latestReceiptSubscriptionError } = useSubscribeToReceipt({
    mode,
    transactionRefs: computed(() => (transactionRef.value ? [transactionRef.value] : undefined)),
    enabled: computed(() => isAuthenticated.value || isAnonymous.value),
  });

  const enableGetReceipt = computed(() => {
    if (isShareMode.value || isViewMode.value) return true;
    return !!transactionRef.value && !isSettingDemoTransactionRef.value;
  });

  const {
    data: currentReceiptData,
    isSuccess: isGetCurrentReceiptSuccess,
    isLoading: isGetCurrentReceiptLoading,
    isFetching: isGetCurrentReceiptFetching,
    isError: isGetCurrentReceiptError,
    error: getCurrentReceiptError,
    refetch: refetchCurrentReceipt,
  } = useGetCurrentReceipt({
    isAnonymous,
    isAuthenticated,
    receiptMode: mode,
    transactionRef,
    enabled: enableGetReceipt,
  });

  useFullScreenErrorMsg(
    getCurrentReceiptError,
    readonly(
      ref({
        id: 'get-current-receipt-error',
        title: t('Error.GetReceiptError.Title'),
        body: t('Error.GetReceiptError.Body'),
        btnTxt: t('Error.BtnTextRetry'),
        btnAction: () => {
          window.location.reload();
        },
        icon: errorIcon,
      })
    )
  );

  useToastErrorMsg(
    latestReceiptSubscriptionError,
    readonly(
      ref({
        id: 'subscribe-to-receipt-error',
        title: t('Error.ReceiptSubscriptionError.Title'),
        autoCloseInMs: 5000,
      })
    )
  );

  const currentReceipt = computed(() => {
    if (!currentReceiptData.value?.getReceipt) return;
    return clone(currentReceiptData.value?.getReceipt);
  });

  const currentReceiptIsEmpty = computed(() => Object.keys(unref(currentReceipt) || {}).length === 0);
  const currentReceiptIsLoadedButEmpty = computed(() => unref(currentReceiptIsEmpty) && !unref(isGetCurrentReceiptLoading));
  const currentReceiptIsLoaded = computed(() => !!unref(currentReceipt) || unref(currentReceiptIsLoadedButEmpty));
  const currentReceiptHash = computed(() => (currentReceipt.value ? objectHash(currentReceipt.value) : undefined));
  const isCurrentReceiptVoid = computed(() => unref(currentReceipt)?.state === 'VOID');
  const isCurrentReceiptBinaryReceiptIncompleted = computed(
    () => !!unref(currentReceipt)?.isBinaryReceipt && (unref(currentReceipt)?.state === 'INCOMPLETE' || !unref(currentReceipt)?.rawReceiptBinary)
  );
  const isReceiptNotYetLinked = computed(() => unref(currentReceiptIsLoaded) && !unref(currentReceipt)?.userId);

  watch(latestReceiptSubscription, (latestReceiptSubscription) => {
    if (latestReceiptSubscription) {
      // refetch receipt if there is a new update
      // TODO: this can possibly be improved. if the subscription return the same data, we dont need to refetch
      refetchCurrentReceipt();
    }
  });

  watch([isCreateTransactionSuccess], ([isCreateTransactionSuccess]) => {
    if (isCreateTransactionSuccess) refetchCurrentReceipt();
  });

  // Set login country by receipt companies
  watchEffect(() => {
    if (currentReceipt.value?.companies?.baseCountry && !LocalStorageManager.basecountry.value) {
      LocalStorageManager.setBaseCountry(currentReceipt.value?.companies?.baseCountry);
    }
  });

  return {
    currentReceipt,
    currentReceiptHash,
    isCurrentReceiptVoid,
    isCurrentReceiptBinaryReceiptIncompleted,
    isGetCurrentReceiptSuccess,
    isGetCurrentReceiptLoading,
    isGetCurrentReceiptFetching,
    isGetCurrentReceiptError,
    getCurrentReceiptError,
    currentReceiptIsLoadedButEmpty,
    enableGetReceipt,
    isReceiptNotYetLinked,
  };
}
