import { CommandByteSequence, CustomRuleFunction, CustomRuleFunctionReturn } from '../types';
import { ESC, FS, GS, NUL } from './commands';

const horizontalTabPosRule = (rawData: string[]): CustomRuleFunctionReturn => {
  const chunkRawData: string[] = [];
  for (const byte of rawData) {
    if (chunkRawData.length === 32) {
      if (byte === NUL) {
        chunkRawData.push(byte);
      }
      break;
    }

    chunkRawData.push(byte);
    if (byte === NUL) break;
  }

  return {
    parsedArgs: [chunkRawData.map((val) => val.charCodeAt(0))],
    rawData: chunkRawData,
  };
};

const selectCutModeAndCutPaperRule = (rawData: string[]): CustomRuleFunctionReturn => {
  const m = rawData[0].charCodeAt(0);

  const chunkRawData: string[] = [];
  if (m >= 65 && m <= 67) chunkRawData.push(rawData[1]);

  return {
    parsedArgs: [chunkRawData.map((val) => val.charCodeAt(0))],
    rawData: chunkRawData,
  };
};

const bitImageModeRule = (rawData: string[]): CustomRuleFunctionReturn => {
  const [m, nL, nH] = rawData.slice(0, 3).map((val) => val.charCodeAt(0));

  const chunkRawData: string[] = [];
  const dataSize = (() => {
    switch (m) {
      case 0:
      case 1:
        return nL + nH * 256;

      case 32:
      case 33:
        return (nL + nH * 256) * 3;
    }
  })();

  chunkRawData.push(...rawData.slice(3, dataSize + 3));

  return {
    parsedArgs: [chunkRawData.map((val) => val.charCodeAt(0))],
    rawData: chunkRawData,
  };
};

const advancedRasterGraphicModeRule = (rawData: string[]): CustomRuleFunctionReturn => {
  const n = rawData[1].charCodeAt(0);

  const chunkRawData: string[] = [];
  chunkRawData.push(...rawData.slice(4, n + 4));

  return {
    parsedArgs: [chunkRawData.map((val) => val.charCodeAt(0))],
    rawData: chunkRawData,
  };
};

const defineDownloadedBitImageModeRule = (rawData: string[]): CustomRuleFunctionReturn => {
  const [x, y] = rawData.slice(0, 2).map((val) => val.charCodeAt(0));

  const chunkRawData: string[] = [];
  const dataSize = x * y * 8;
  chunkRawData.push(...rawData.slice(4, dataSize + 4));

  return {
    parsedArgs: [chunkRawData.map((val) => val.charCodeAt(0))],
    rawData: chunkRawData,
  };
};

const defineNVBitImageModeRule = (rawData: string[]): CustomRuleFunctionReturn => {
  const n = rawData[0].charCodeAt(0);

  const chunkRawData: string[] = [];

  let parsingPosition = 0;
  for (let index = 0; index < n; index++) {
    const [xL, xH, yL, yH] = rawData.slice(parsingPosition, 4).map((val) => val.charCodeAt(0));
    const k = (xL + Math.min(xH, 3) * 256) * (yL + Math.min(yH, 1) * 256) * 8;
    chunkRawData.push(...rawData.slice(parsingPosition, k + 4));

    parsingPosition += k + 4;
  }

  return {
    parsedArgs: [chunkRawData.map((val) => val.charCodeAt(0))],
    rawData: chunkRawData,
  };
};

const printRasterBitImageModeRule = (rawData: string[]): CustomRuleFunctionReturn => {
  const [, xL, xH, yL, yH] = rawData.slice(0, 5).map((val) => val.charCodeAt(0));

  const chunkRawData: string[] = [];
  const dataSize = (xL + xH * 256) * (yL + yH * 256);
  chunkRawData.push(...rawData.slice(5, dataSize + 5));

  return {
    parsedArgs: [chunkRawData.map((val) => val.charCodeAt(0))],
    rawData: chunkRawData,
  };
};

const specifyGraphicDataRule = (rawData: string[]): CustomRuleFunctionReturn => {
  const [, , , fn] = rawData.map((val) => val.charCodeAt(0));

  const chunkRawData: string[] = [];
  switch (fn) {
    case 112: {
      const [xL, xH, yL, yH] = rawData.slice(8).map((val) => val.charCodeAt(0));
      const dataSize = Math.round((xL + xH * 256 + 7) / 8) * (yL + yH * 256);
      chunkRawData.push(...rawData.slice(12, dataSize + 12));
      break;
    }

    default:
      break;
  }

  return {
    parsedArgs: [chunkRawData.map((val) => val.charCodeAt(0))],
    rawData: chunkRawData,
  };
};

