export const getCustomRule = (command: CommandByteSequence): CustomRuleFunction | undefined => {
  switch (command) {
    case `${ESC}\x44`:
      return horizontalTabPosRule;

    case `${ESC}\x2a`:
      return bitImageModeRule;

    case `${ESC}\x2e`:
      return advancedRasterGraphicModeRule;

    case `${GS}\x2a`:
      return defineDownloadedBitImageModeRule;

    case `${GS}\x56`:
      return selectCutModeAndCutPaperRule;

    case `${FS}\x71`:
      return defineNVBitImageModeRule;

    case `${GS}\x76\x30`:
      return printRasterBitImageModeRule;

    case `${GS}\x28\x4c`:
    case `${GS}\x38\x4c`:
      return specifyGraphicDataRule;

    case `${GS}\x6b`:
      return printBarcodeRule;

    case `${GS}\x28\x6b`:
      return printBarcodePDF417Rule;

    default:
      return undefined;
  }
};
