<template>
  <div class="bg-white">
    <div class="flex justify-between w-11/12 py-4 mx-auto" data-cy="receipt-detail-dialog">
      <div class="flex align-center">
        <div class="cursor-pointer my-auto pt-1" @click="handleClose" v-if="isAuthenticated" data-cy="back-receipt-button">
          <font-awesome-icon icon="fa-solid fa-chevron-left" class="mr-3 gfold:text-2xl text-3xl" />
        </div>
        <p class="gfold:text-xl font-poppins text-2xl font-medium my-auto" :class="isPreSettlementReceipt ? 'text-red-1000' : ''" data-cy="receipt-title">
          {{ isPreSettlementReceipt ? 'Bill Preview' : data?.Transaction?.TransactionTitle ?? 'Sales Receipt' }}
        </p>
      </div>
      <div v-if="isAuthenticated" class="flex">
        <button v-wave v-if="!isPreSettlementReceipt" class="flex rounded-full p-2 w-10 h-10 items-center place-content-center">
          <font-awesome-icon @click="generatePdf" icon="fa-solid fa-download" class="gfold:text-2xl text-3xl" data-cy="receipt-detail-download" />
        </button>
        <button
          v-wave
          v-if="route.query.mode !== 'share' && !featureState.demoMode && !featureState.sandboxMode"
          class="flex rounded-full p-2 w-10 h-10 items-center place-content-center"
        >
          <font-awesome-icon icon="fa-solid fa-share-nodes" class="gfold:text-2xl text-3xl" @click="openShareReceipt" data-cy="receipt-detail-share" />
        </button>
      </div>
    </div>
  </div>

  <div class="bg-white w-full flex-auto pb-20">
    <div class="animate-pulse flex flex-col px-5 py-5 gap-y-5 h-full" v-if="isGetReceiptTemplateForCompanyLoading && !rawReceiptBinary">
      <div class="flex">
        <div class="w-20 h-20 border-1 rounded-lg bg-[#bdbdbd] flex justify-center items-center"></div>
        <div class="flex flex-col gap-y-2 px-2 text-left flex-grow">
          <div class="w-full h-6 rounded-md bg-[#bdbdbd]"></div>
          <div class="w-full h-6 rounded-md bg-[#bdbdbd]"></div>
        </div>
      </div>
      <div class="flex">
        <div class="w-full h-6 rounded-md bg-[#bdbdbd]"></div>
      </div>
      <div class="flex justify-center">
        <div class="w-14 h-14 rounded-md bg-[#bdbdbd]"></div>
      </div>
      <div class="flex flex-col gap-y-4 px-2 text-left">
        <div class="w-full h-8 rounded-md bg-[#bdbdbd]"></div>
        <div class="w-full h-8 rounded-md bg-[#bdbdbd]"></div>
        <div class="w-full h-8 rounded-md bg-[#bdbdbd]"></div>
        <div class="w-full h-8 rounded-md bg-[#bdbdbd]"></div>
      </div>
      <div class="flex flex-col mt-5 gap-y-2 px-2 text-left">
        <div class="w-full h-6 rounded-md bg-[#bdbdbd]"></div>
        <div class="w-full h-6 rounded-md bg-[#bdbdbd]"></div>
        <div class="w-full h-6 rounded-md bg-[#bdbdbd]"></div>
        <div class="w-full h-6 rounded-md bg-[#bdbdbd]"></div>
        <div class="w-full h-6 rounded-md bg-[#bdbdbd]"></div>
        <div class="w-full h-6 rounded-md bg-[#bdbdbd]"></div>
      </div>
    </div>

    <EscPosReceiptRenderer v-else-if="renderWithEscPosRenderer" :data="rawReceiptBinaryData" :company-data="companyDataValue" />
    <ReceiptRenderer
      v-else
      :data="data"
      :companyData="companyDataValue"
      :isPreSettlementReceipt="isPreSettlementReceipt"
      :template="template"
      :sandboxMode="featureState.sandboxMode"
    />

    <div v-if="isAnonymous" class="h-40 bg-white"></div>
  </div>

  <!--
    - Hide whatsapp receipt prompt using the class style by default since the mustache receipt template
    - will be the one that trigger an update to this class to make it visible later on 
    -->
  <WhatsappReceiptPrompt class="hidden" id="whatsapp-receipt-prompt" :show="true" :topic="topic" @send-whatsapp="sendWhatsapp" @close-modal="closeModal" />

  <ShareReceiptToggle :showShareReceipt="showShareReceipt" @toggle-share-receipt="toggleShareReceipt" :shareUrl="shareUrl" />
</template>

<script setup lang="ts">
import { useAlertStore } from '@/stores/alert';
import { useFeatureStore } from '@/stores/feature';
import { useAuthManager } from '@/utils/auth';
import { ENV } from '@/utils/env';
import { GetReceiptQuery } from '@pi-xcels/graphql/types';
import { downloadBlob, randomId } from '@pi-xcels/utils';
import html2canvas from 'html2canvas';
import jsPDF, { jsPDFOptions } from 'jspdf';
import { computed, ref, toRefs } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute } from 'vue-router';
import { useReceiptDetailCloseHandler } from './hooks/useReceiptDetailCloseHandler';

const route = useRoute();
const featureState = useFeatureStore();
const alertState = useAlertStore();
const { isAnonymous, isAuthenticated } = useAuthManager();

const props = defineProps<{
  data?: NonNullable<GetReceiptQuery['getReceipt']>['receipt'];
  rawReceiptBinary?: NonNullable<GetReceiptQuery['getReceipt']>['rawReceiptBinary'];
  companyData?: NonNullable<GetReceiptQuery['getReceipt']>['companies'];
  isCurrentReceipt: boolean;
  state?: NonNullable<GetReceiptQuery['getReceipt']>['state'];
  isGetReceiptTemplateForCompanyLoading: boolean;
  isLinkReceiptToUserLoading: boolean;
  template?: string;
}>();
const { data: fetchedData, rawReceiptBinary: fetchedRawReceiptBinary, companyData, isCurrentReceipt, state } = toRefs(props);

const receiptData = computed(() => fetchedData?.value);
const rawReceiptBinaryData = computed(() => fetchedRawReceiptBinary?.value);
const renderWithEscPosRenderer = computed(() => rawReceiptBinaryData.value && !receiptData.value.renderWithMustaceTemplate);
const companyDataValue = computed(() => companyData?.value);
const shareUrl = computed(() => `${ENV.NX_ROOT_URL}/receipt?v=${receiptData.value.receiptId}&mode=share`);
const isPreSettlementReceipt = computed(() => (state?.value || '').toLowerCase() === 'incomplete');

const { t } = useI18n();
const topic = ref('');
const showShareReceipt = ref(false);

const { handleClose } = useReceiptDetailCloseHandler({ isCurrentReceipt });

const closeModal = () => {
  const whatsappPromptElement = document.getElementById('whatsapp-receipt-prompt');
  if (whatsappPromptElement) {
    whatsappPromptElement.classList.remove('visible');
    whatsappPromptElement.classList.add('hidden');
  }
};

/**
 * Normalize all svg by set it explicit width and height, then convert it to base64.
 * This step is necessary due to a glitch in the Apple (iOS, macOS) environment
 * when rendering SVG during the process of downloading receipts PDF.
 */
const normalizeAllSvg = async () => {
  const targetElem = document.getElementById('receipt-detail');
  const imgSvg: NodeListOf<HTMLImageElement> = targetElem!.querySelectorAll('img[src$=".svg"]');
  const svgElem = targetElem!.getElementsByTagName('svg');

  for (let i = 0; i < svgElem.length; i++) {
    const curWidth = svgElem[i].getAttribute('width');
    const curHeight = svgElem[i].getAttribute('height');

    svgElem[i].setAttribute('width', curWidth ?? '100%');
    svgElem[i].setAttribute('height', curHeight ?? '100%');
  }

  await Promise.all(
    Array.from(imgSvg).map(async (image) => {
      image.src = await convertSvgToBase64(image);
    })
  );
};

const convertSvgToBase64 = (img: HTMLImageElement): Promise<string> => {
  return new Promise((resolve, reject) => {
    let xhr = new XMLHttpRequest();
    xhr.responseType = 'document';
    xhr.onload = () => {
      let svg = xhr.response.getElementsByTagName('svg')[0];
      let canvas = document.createElement('canvas');
      let ctx = canvas.getContext('2d');
      let imgWidth = img.width || svg.width.baseVal.value;
      let imgHeight = img.height || svg.height.baseVal.value;

      canvas.width = imgWidth;
      canvas.height = imgHeight;

      let DOMURL = window.URL || window.webkitURL || window;
      let data = new XMLSerializer().serializeToString(svg);
      let imgBlob = new Blob([data], { type: 'image/svg+xml;charset=utf-8' });
      let url = DOMURL.createObjectURL(imgBlob);
      let imgObj = new Image();

      imgObj.onload = () => {
        ctx!.drawImage(imgObj, 0, 0, imgWidth, imgHeight);
        let base64data = canvas.toDataURL('image/png');
        DOMURL.revokeObjectURL(url);
        resolve(base64data);
      };

      imgObj.onerror = () => {
        reject(xhr.statusText);
      };

      imgObj.src = url;
    };
    xhr.open('GET', img.src, true);
    xhr.send();
  });
};

const generatePdf = async () => {
  const downloadingMsgId = randomId();
  alertState.addToastMsg({
    id: downloadingMsgId,
    title: 'Your receipt is being downloaded. Please wait a moment.',
    autoCloseInMs: 5000,
  });

  if (!rawReceiptBinaryData.value) {
    await normalizeAllSvg();
  }

  const canvas = await html2canvas(document.getElementById('receipt-detail')!, {
    logging: false,
    useCORS: true,
    scale: 1.8,
  });

  const imgData = canvas.toDataURL();
  const pdfOptions: jsPDFOptions = {
    orientation: 'p',
    unit: 'px',
    format: [document.getElementById('receipt-detail')!.clientWidth + 20, document.getElementById('receipt-detail')!.clientHeight + 70],
    compress: false,
  };

  const pdf = new jsPDF(pdfOptions);
  pdf.addImage(
    imgData,
    'JPEG',
    10,
    35,
    document.getElementById('receipt-detail')!.clientWidth,
    document.getElementById('receipt-detail')!.clientHeight,
    undefined,
    'FAST'
  );

  pdf.setFontSize(30);
  pdf.text(isPreSettlementReceipt.value ? 'Bill Preview' : receiptData?.value?.Transaction?.TransactionTitle ?? 'Sales Receipt', 15, 30);

  const isDownloadSuccess = await downloadBlob(pdf.output('blob'), `Receipt_${receiptData?.value?.ReceiptNo}.pdf`);
  alertState.removeAlertMessage(downloadingMsgId);
  if (isDownloadSuccess) {
    alertState.addToastMsg({
      id: randomId(),
      title: 'Your receipt downloaded successfully.',
      autoCloseInMs: 5000,
    });
  } else {
    alertState.addToastMsg({
      id: randomId(),
      title: 'Something wrong happened, failed to download your receipt.',
      autoCloseInMs: 5000,
    });
  }
};

const toggleShareReceipt = () => {
  showShareReceipt.value = !showShareReceipt.value;
};

const openShareReceipt = () => {
  if (navigator.share) {
    navigator
      .share({
        url: shareUrl.value,
      })
      .catch(console.error);
  } else {
    toggleShareReceipt();
  }
};

const sendWhatsapp = (topic: string) => {
  const whatsappNumber =
    companyData?.value?.whatsappNumber && !(featureState.demoMode || featureState.sandboxMode) ? companyData?.value?.whatsappNumber : '6281119125125';

  const message = (() => {
    let messageBodyText = '';
    switch (topic) {
      case 'issuePurchase':
        messageBodyText = t('WhatsAppPrompt.IssuePurchaseText');
        break;
      case 'feedback':
        messageBodyText = t('WhatsAppPrompt.FeedbackText');
        break;
      case 'error':
        messageBodyText = t('WhatsAppPrompt.ReceiptErrorText');
        break;
      case 'issueAccount':
        messageBodyText = t('WhatsAppPrompt.AccountIssueText');
        break;
      default:
        messageBodyText = '';
        break;
    }

    return `${messageBodyText}${t('WhatsAppPrompt.ReceiptNumber', {
      receiptNo: receiptData.value.ReceiptNo,
    })}`;
  })();

  window.open(`https://wa.me/${whatsappNumber}?text=${encodeURIComponent(message)}`, '__blank');
};
</script>
