import { useCreateTransaction } from '@/api/receipt';
import { useUserStore } from '@/stores/user';
import { useAuthManager } from '@/utils/auth';
import { useFullScreenErrorMsg } from '@/utils/hooks/useErrorHandling';
import errorIcon from '@pi-xcels/ui-components/assets/images/error.svg';
import { computed, readonly, ref } from 'vue';
import { useI18n } from 'vue-i18n';

export function useWaitingRoomTransactionHandler() {
  const { isAuthenticated } = useAuthManager();
  const userState = useUserStore();
  const { t } = useI18n();

  const {
    mutateAsync: createTransaction,
    isSuccess: isCreateTransactionSuccess,
    isError: isCreateTransactionError,
    error: createTransactionError,
    isLoading: isCreateTransactionLoading,
    data: createTransactionResp,
  } = useCreateTransaction({
    // retry on failure
    retry: 3,
  });

  useFullScreenErrorMsg(
    createTransactionError,
    readonly(
      ref({
        id: 'create-transaction-error',
        title: t('Error.CreateTransactionError.Title'),
        body: t('Error.CreateTransactionError.Body'),
        btnTxt: t('Error.BtnTextRetry'),
        btnAction: () => {
          window.location.reload();
        },
        icon: errorIcon,
      })
    )
  );

  const transaction = computed(() => createTransactionResp.value?.createTransaction);
  const isMyTransaction = computed(() => isAuthenticated.value && transaction.value?.userId === userState.id);
  const isNotMyTransaction = computed(() => isAuthenticated.value && !!transaction.value && !isMyTransaction.value);
  const isTransactionUtilised = computed(() => !!transaction.value?.utilised);
  const isReceiptExpired = computed(() => !!transaction.value?.expired.isReceiptExpired);
  const isReceiptLinkExpired = computed(() => !!transaction.value?.expired.isReceiptLinkExpired);
  const isReceiptOrReceiptLinkExpired = computed(() => isReceiptExpired.value || isReceiptLinkExpired.value);
  const isTransactionVoided = computed(() => !!transaction.value?.isVoided);
  const isEmpTransaction = computed(() => !!transaction.value?.isEmpTransaction);
  const openUtilisedTransactionPrompt = computed(() => isTransactionUtilised.value && isEmpTransaction.value);
  const isTransactionLoaded = computed(() => !!transaction.value);

  return {
    createTransaction,
    isCreateTransactionSuccess,
    isCreateTransactionError,
    createTransactionError,
    isCreateTransactionLoading,
    createTransactionResp,
    transaction,
    isMyTransaction,
    isNotMyTransaction,
    isTransactionUtilised,
    isReceiptExpired,
    isReceiptLinkExpired,
    isReceiptOrReceiptLinkExpired,
    isTransactionVoided,
    isEmpTransaction,
    openUtilisedTransactionPrompt,
    isTransactionLoaded,
  };
}
