<template>
  <!-- Receipt Detail -->
  <div
    v-if="(isAuthenticated || isAnonymous) && receiptOpen"
    class="fixed bg-gray-100 top-0 bottom-0 left-0 right-0 flex justify-center overflow-y-scroll overflow-x-hidden"
  >
    <div class="flex flex-col flex-grow max-w-md" id="receipt-template-wrap">
      <ReceiptDetail
        :key="currentReceiptHash"
        :state="currentReceipt?.state"
        :rawReceiptBinary="currentReceipt?.rawReceiptBinary"
        :data="currentReceipt?.receipt"
        :company-data="currentReceipt?.companies"
        :isCurrentReceipt="true"
        :isGetReceiptTemplateForCompanyLoading="isGetReceiptTemplateForCompanyLoading"
        :isLinkReceiptToUserLoading="isLinkReceiptToUserLoading"
        :template="template"
      />
      <div v-if="featureState.sandboxMode" class="h-40 bg-white"></div>
    </div>
  </div>
  <!-- Showing Invalid Hash Prompt / Share Receipt must Logged in Prompt / Expired Receipt Prompt -->
  <ExtendedContentView
    v-else-if="extendedContentView"
    :title="extendedContentView?.title"
    :subtitle="extendedContentView.subtitle"
    :button="extendedContentView.button"
  />
  <ReceiptLoader v-else-if="showReceiptLoader" />
  <ReceiptFallback
    v-else
    :is-transaction-utilised="isTransactionUtilised"
    :is-nfc="isNfc"
    :open-utilised-transaction-prompt="openUtilisedTransactionPrompt"
    :transaction="transaction"
  />

  <ReceiptPrompts :employee="employee" :receipt-open="receiptOpen" :current-receipt="currentReceipt" :transaction-ref="transactionRef" />

  <!-- a hack for checking if linkReceiptToUser has been called -->
  <div data-cy="called-linked-receipt" v-if="isLinkReceiptToUserError || isLinkReceiptToUserLoading || linkReceiptToUserResp" class="hidden"></div>
</template>

<script setup lang="ts">
import { useAlertStore } from '@/stores/alert';
import { useFeatureStore } from '@/stores/feature';
import { useUiStore } from '@/stores/ui';
import { useAuthManager } from '@/utils/auth';
import { useCountdownTimer } from '@/utils/hooks/useCountdownTimer';
import normalizeQueryValue from '@/utils/route';
import { demoTransactionRefs } from '@pi-xcels/utils';
import { computed, onMounted, readonly, ref, unref, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute, useRouter } from 'vue-router';
import { useWaitingRoomCurrentReceiptHandler } from './hooks/useWaitingRoomCurrentReceiptHandler';
import { useWaitingRoomLinkReceiptHandler } from './hooks/useWaitingRoomLinkReceiptHandler';
import { useWaitingRoomReceiptTemplateHandler } from './hooks/useWaitingRoomReceiptTemplateHandler';
import { useWaitingRoomTransactionHandler } from './hooks/useWaitingRoomTransactionHandler';

const { t } = useI18n();
const route = useRoute();
const router = useRouter();

const alertState = useAlertStore();
const uiState = useUiStore();
const featureState = useFeatureStore();
const { isAuthenticated, isAnonymous, isAuthenticating } = useAuthManager();
const { restart: restartReceiptTimer, expired: isReceiptNotReadyInTime, resetAndStop: stopReceiptTimer } = useCountdownTimer(readonly(ref(4000)));

const demoTransactionRef = ref('');

const mode = computed(() => normalizeQueryValue(route?.query.mode));
const isNfc = computed(() => mode.value === 'nfc');
const isShareMode = computed(() => mode.value === 'share');
const isViewMode = computed(() => mode.value === 'view');
const isProductionMode = computed(() => !(featureState.sandboxMode || featureState.demoMode));

const {
  createTransaction,
  transaction,
  isCreateTransactionLoading,
  isCreateTransactionSuccess,
  isReceiptLinkExpired,
  isCreateTransactionError,
  isTransactionVoided,
  isReceiptExpired,
  isReceiptOrReceiptLinkExpired,
  isTransactionUtilised,
  openUtilisedTransactionPrompt,
  isTransactionLoaded,
} = useWaitingRoomTransactionHandler();

const transactionRef = computed<string>(() => transaction.value?.ref || normalizeQueryValue(route?.query.v));
const isSettingDemoTransactionRef = computed(() => {
  const isUsingDemoTransactionRef = demoTransactionRefs.includes(transactionRef.value);
  if (!isUsingDemoTransactionRef) return false;
  if (featureState.demoModeIsLoading) return true;
  return featureState.demoMode;
});

const {
  currentReceipt,
  currentReceiptHash,
  currentReceiptIsLoadedButEmpty,
  isCurrentReceiptBinaryReceiptIncompleted,
  isCurrentReceiptVoid,
  isGetCurrentReceiptFetching,
  isGetCurrentReceiptLoading,
  enableGetReceipt,
  isReceiptNotYetLinked,
} = useWaitingRoomCurrentReceiptHandler({
  transactionRef,
  isSettingDemoTransactionRef,
  isCreateTransactionSuccess,
});

const { employee, isLinkReceiptToUserError, isLinkReceiptToUserLoading, linkReceiptToUserResp } = useWaitingRoomLinkReceiptHandler({
  isTransactionUtilised,
  isTransactionVoided,
  isTransactionLoaded,
  transactionRef,
  isReceiptOrReceiptLinkExpired,
  isReceiptNotYetLinked,
});

const { template, receiptTemplateIsReady, isGetReceiptTemplateForCompanyLoading } = useWaitingRoomReceiptTemplateHandler({
  currentReceipt,
});

const canShowReceipt = computed(() => {
  const isBinaryReceiptIncompleted =
    unref(currentReceipt)?.state === 'INCOMPLETE' && unref(currentReceipt)?.isBinaryReceipt && !unref(currentReceipt)?.rawReceiptBinary;

  const transactionAndReceiptIsExist = unref(transaction)?.id && unref(transaction)?.id === unref(currentReceipt)?.transactionId;

  return (
    !unref(isCreateTransactionLoading) &&
    unref(currentReceipt)?.transactionId &&
    (transactionAndReceiptIsExist || unref(isShareMode) || unref(isViewMode)) &&
    !isBinaryReceiptIncompleted &&
    unref(currentReceipt)?.state !== 'VOID' &&
    !unref(transaction)?.isVoided &&
    !unref(isReceiptLinkExpired)
  );
});

const receiptOpen = computed(() => {
  return !!(unref(canShowReceipt) && unref(receiptTemplateIsReady));
});

const showLoginDialog = () => {
  uiState.showLoginDialog = true;
};

const clearDemoTransactionRef = () => {
  if (demoTransactionRef.value !== '') {
    demoTransactionRef.value = '';
  }
};

watch(isCreateTransactionError, (isError) => {
  if (!isError) return;
  clearDemoTransactionRef();
});

const extendedContentView = computed(() => {
  if (isTransactionVoided.value || isCurrentReceiptVoid.value) {
    return {
      title: t('ReceiptLoader.VoidReceiptTitle'),
      subtitle: t('ReceiptLoader.VoidReceiptSubtitle'),
      button: {
        text: t('ReceiptLoader.OKButton'),
        action(): void {
          router.push({ path: '/' });
        },
      },
    };
  }
  if (isCurrentReceiptBinaryReceiptIncompleted.value) {
    return {
      title: t('ReceiptLoader.NFCInfo'),
      button: {
        text: t('ReceiptLoader.OKButton'),
        action(): void {
          router.push({ path: '/' });
        },
      },
    };
  }
  if (isReceiptOrReceiptLinkExpired.value) {
    return {
      title: isReceiptExpired.value ? t('ReceiptLoader.SharedReceiptText') : t('ReceiptLoader.ReceiptLinkExpired'),
      button: {
        text: t('ReceiptLoader.OKButton'),
        action(): void {
          router.push({ path: '/' });
        },
      },
    };
  }

  if (currentReceiptIsLoadedButEmpty.value) {
    if (isViewMode.value) {
      return {
        title: t('ReceiptLoader.InvalidHashText'),
        button: {
          text: t('ReceiptLoader.OKButton'),
          action(): void {
            router.push({ path: '/' });
          },
        },
      };
    }
    if (isShareMode.value) {
      if (unref(isAuthenticated)) {
        return {
          title: t('ReceiptLoader.InvalidHashText'),
          button: {
            text: t('ReceiptLoader.OKButton'),
            action(): void {
              router.push({ path: '/' });
            },
          },
        };
      } else if (unref(isAnonymous)) {
        return {
          title: t('ReceiptLoader.SharedReceiptText'),
          button: {
            text: t('ReceiptLoader.LoginButton'),
            action(): void {
              showLoginDialog();
            },
          },
        };
      }
    }
  }

  return null;
});

// Show login dialog after exceed loginPopupDelay time in ms
const loginPopupDelay = computed(() => featureState.loginPopupDelay);
const { restart: restartLoginDialogTimer, expired: isLoginDialogTimerExpired, resetAndStop: stopLoginDialogTimer } = useCountdownTimer(loginPopupDelay);
watch(
  [currentReceipt, extendedContentView, isAnonymous, isProductionMode],
  ([currentReceipt, extendedContentView, isAnonymous, isProductionMode]) => {
    if (extendedContentView) return;
    if (!isProductionMode) return;
    if (!isAnonymous) return;
    if (!currentReceipt) return;
    restartLoginDialogTimer();
  },
  { immediate: true }
);
watch([isLoginDialogTimerExpired, receiptOpen], ([isLoginDialogTimerExpired, receiptOpen]) => {
  if (isLoginDialogTimerExpired && receiptOpen) showLoginDialog();
});

watch(
  [currentReceipt, extendedContentView, isShareMode, isViewMode, isAuthenticated, receiptOpen],
  ([currentReceipt, extendedContentView, isShareMode, isViewMode, isAuthenticated, receiptOpen]) => {
    if (extendedContentView) return;
    if (!currentReceipt) return;
    if (isShareMode || isViewMode) return;
    if (!isAuthenticated) return;
    if (!receiptOpen) return;
    alertState.addToastMsg({
      id: t('SaveReceipt.SaveInfo') + currentReceipt.transactionRef,
      title: t('SaveReceipt.SaveInfo'),
      autoCloseInMs: 5000,
    });
  },
  { immediate: true }
);

watch(
  [isAuthenticated],
  ([isAuthenticated]) => {
    if (!isAuthenticated) return;
    stopLoginDialogTimer();
    uiState.showLoginDialog = false;
  },
  { immediate: true }
);

const showReceiptLoader = computed(() => {
  if (isReceiptNotReadyInTime.value) return false;
  if (isCreateTransactionLoading.value) return true;
  if (isGetCurrentReceiptLoading.value && enableGetReceipt.value) return true;
  if (isGetCurrentReceiptFetching.value) return true;
  if (isSettingDemoTransactionRef.value) return true;
  if (!receiptTemplateIsReady.value) return true;
  return false;
});

watch(
  [isAuthenticating, isAuthenticated, isAnonymous, demoTransactionRef, isSettingDemoTransactionRef, isViewMode, isShareMode],
  ([currIsAuthenticating, currIsAuthenticated, currIsAnonymous, currDemoTransactionRef, currIsSettingDemoTransactionRef, currIsViewMode, currIsShareMode]) => {
    if (currIsAuthenticating) return;
    if (!currIsAuthenticated && !currIsAnonymous) return;
    // if demo transaction ID is not yet handled, exit
    if (currIsSettingDemoTransactionRef) return;
    if (currIsViewMode) return;
    if (currIsShareMode) return;

    createTransaction({
      variables: {
        data: {
          ref: transactionRef.value,
          demoTransactionRef: currDemoTransactionRef,
        },
      },
    });
  },
  { immediate: true }
);

watch(
  isAuthenticated,
  (curr, prev) => {
    if (curr && !prev) {
      restartReceiptTimer();
    }
  },
  { immediate: true }
);

watch(
  receiptOpen,
  (open) => {
    if (open) stopReceiptTimer();
    else restartReceiptTimer();
  },
  { immediate: true }
);

watch(
  [isSettingDemoTransactionRef],
  (isSettingDemoTransaction) => {
    // If current v query parameter value is one of the demo transaction id
    // and the app is in demo mode then change the v query parameter value to
    // current datetime value and redirect the user there instead
    if (isSettingDemoTransaction && demoTransactionRefs.includes(transactionRef.value)) {
      const currentDateTime = Date.now().toString();
      demoTransactionRef.value = transactionRef.value;

      router.replace({
        path: '/receipt',
        query: {
          v: currentDateTime,
        },
      });
      restartReceiptTimer();
    }
  },
  { immediate: true }
);

onMounted(async () => {
  document.body.style.overflow = 'unset';
});
</script>
