import { CommandByteSequence, ExtractDataFunctionReturn, ParsedArgs } from '../types';
import { ESC, GS } from './commands';
import * as Graphics from './graphics';

export const extractData = (command: CommandByteSequence, parsedArgs?: ParsedArgs): ExtractDataFunctionReturn | undefined => {
  switch (command) {
    // case `${ESC}\x44`:
    //   return horizontalTabPosRule;

    case `${ESC}\x2a`:
      return Graphics.selectBitImageModeCommand(parsedArgs);

    // case `${ESC}\x2e`:
    //   return advancedRasterGraphicModeRule;

    // case `${GS}\x2a`:
    //   return defineDownloadedBitImageModeRule;

    // case `${FS}\x71`:
    //   return defineNVBitImageModeRule;

    case `${GS}\x76\x30`:
      return Graphics.printRasterBitImageModeCommand(parsedArgs);

    case `${GS}\x28\x4c`:
    case `${GS}\x38\x4c`:
      return Graphics.specifyGraphicDataCommand(parsedArgs);

    // case `${GS}\x6b`:
    //   return printBarcodeRule;

    // case `${GS}\x28\x6b`:
    //   return printBarcodePDF417Rule;

    default:
      return undefined;
  }
};
