import { useLinkReceiptToUser } from '@/api/receipt';
import { useAuthManager } from '@/utils/auth';
import { useFullScreenErrorMsg } from '@/utils/hooks/useErrorHandling';
import normalizeQueryValue from '@/utils/route';
import errorIcon from '@pi-xcels/ui-components/assets/images/error.svg';
import { Ref, computed, readonly, ref, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute } from 'vue-router';

export function useWaitingRoomLinkReceiptHandler({
  isTransactionLoaded,
  isTransactionUtilised,
  isTransactionVoided,
  transactionRef,
  isReceiptOrReceiptLinkExpired,
  isReceiptNotYetLinked,
}: {
  isTransactionLoaded: Readonly<Ref<boolean>>;
  isTransactionVoided: Readonly<Ref<boolean>>;
  isTransactionUtilised: Readonly<Ref<boolean>>;
  transactionRef: Readonly<Ref<string>>;
  isReceiptOrReceiptLinkExpired: Readonly<Ref<boolean>>;
  isReceiptNotYetLinked: Readonly<Ref<boolean>>;
}) {
  const { isAuthenticated } = useAuthManager();
  const route = useRoute();
  const mode = computed(() => normalizeQueryValue(route?.query.mode));
  const isViewMode = computed(() => mode.value === 'view');
  const { t } = useI18n();

  const {
    mutateAsync: linkReceiptToUser,
    isSuccess: isLinkReceiptToUserSuccess,
    isError: isLinkReceiptToUserError,
    isLoading: isLinkReceiptToUserLoading,
    data: linkReceiptToUserResp,
    error: linkReceiptToUserError,
  } = useLinkReceiptToUser();

  useFullScreenErrorMsg(
    linkReceiptToUserError,
    readonly(
      ref({
        id: 'link-receipt-to-user-error',
        title: t('Error.LinkReceiptError.Title'),
        body: t('Error.LinkReceiptError.Body'),
        btnTxt: t('Error.BtnTextRetry'),
        btnAction: () => {
          window.location.reload();
        },
        icon: errorIcon,
      })
    )
  );

  watch(
    [isAuthenticated, isTransactionVoided, isTransactionLoaded, isTransactionUtilised, isViewMode, isReceiptOrReceiptLinkExpired, isReceiptNotYetLinked],
    ([isAuthenticated, isTransactionVoided, isTransactionLoaded, isTransactionUtilised, isViewMode, isReceiptOrReceiptLinkExpired, isReceiptNotYetLinked]) => {
      if (
        isAuthenticated &&
        isTransactionLoaded &&
        !isTransactionVoided &&
        !isTransactionUtilised &&
        !isViewMode &&
        !isReceiptOrReceiptLinkExpired &&
        isReceiptNotYetLinked
      ) {
        linkReceiptToUser({ variables: { where: { transactionRef: transactionRef.value } } });
      }
    },
    { immediate: true }
  );

  const employee = computed(() => linkReceiptToUserResp.value?.linkReceiptToUser?.employee);

  return {
    employee,
    isLinkReceiptToUserSuccess,
    isLinkReceiptToUserError,
    isLinkReceiptToUserLoading,
    linkReceiptToUserResp,
  };
}
