import { useAlertStore } from '@/stores/alert';
import { useAuthManager } from '@/utils/auth';
import { useFullScreenErrorMsg } from '@/utils/hooks/useErrorHandling';
import { useGetReceiptTemplateForCompany } from '@/utils/receipt';
import errorIcon from '@pi-xcels/ui-components/assets/images/error.svg';
import { computed, unref } from 'vue';
import { useI18n } from 'vue-i18n';
import { useReceiptDetailCloseHandler } from './useReceiptDetailCloseHandler';
import { useWaitingRoomCurrentReceiptHandler } from './useWaitingRoomCurrentReceiptHandler';

export function useWaitingRoomReceiptTemplateHandler({
  currentReceipt,
}: {
  currentReceipt: ReturnType<typeof useWaitingRoomCurrentReceiptHandler>['currentReceipt'];
}) {
  const { t } = useI18n();
  const { isAuthenticated } = useAuthManager();
  const alertState = useAlertStore();

  const companyName = computed(() => currentReceipt.value?.receipt.Store?.CompanyRef || currentReceipt.value?.receipt.Store?.CompanyName);
  const {
    isLoading: isGetReceiptTemplateForCompanyLoading,
    template,
    isError: isGetReceiptTemplateForCompanyError,
    noTemplateFound,
    error: getReceiptTemplateForCompanyError,
  } = useGetReceiptTemplateForCompany({
    companyName,
    enabled: computed(() => !!currentReceipt.value && !!companyName.value && currentReceipt.value?.receipt?.renderWithMustaceTemplate),
  });

  const { handleClose } = useReceiptDetailCloseHandler({ isCurrentReceipt: computed(() => !!currentReceipt.value) });
  useFullScreenErrorMsg(
    computed(() => noTemplateFound.value || getReceiptTemplateForCompanyError.value),
    computed(() => ({
      id: 'get-receipt-template-error',
      title: t('Error.GetReceiptTemplateError.Title'),
      body: t('Error.GetReceiptTemplateError.Body'),
      btnTxt: isAuthenticated.value ? t('Error.BtnTextOK') : t('Error.BtnTextRetry'),
      btnAction: isAuthenticated.value
        ? () => {
            alertState.removeAlertMessage('get-receipt-template-error');
            handleClose();
          }
        : () => {
            window.location.reload();
          },
      icon: errorIcon,
    }))
  );

  const receiptTemplateIsReady = computed(
    () =>
      !!(
        unref(template) ||
        // template is not needed for binary receipt, thus we can consider it as ready
        unref(currentReceipt)?.isBinaryReceipt ||
        unref(currentReceipt)?.rawReceiptBinary
      )
  );

  return { template, receiptTemplateIsReady, isGetReceiptTemplateForCompanyLoading, isGetReceiptTemplateForCompanyError };
}
