import cloneDeep from 'just-clone';

import { CR, ESC, FF, HT, LF } from '../parser/commands';
import { PrinterContext } from '../renderer/printer';
import { encodeTextData } from '../renderer/text';
import { walkAST } from '../renderer/util';
import { AnalyzerNode, ChunkData, CommandByteSequence, DataChunk, Description, RuleTree } from '../types';

const printCommands = [`${FF}`, `${LF}`, `${CR}`, `${ESC}\x4a`, `${ESC}\x64`, `${HT}`];

const maxSameCharacterSequence = (textData: string) => {
  let lastByte = '';
  const countArr = [];
  let count = 0;

  for (const char of textData) {
    if (lastByte !== char) {
      lastByte = char;
      countArr.push(count);
      count = 0;
    } else count += 1;
  }

  return Math.max(...countArr);
};

const findCommand = (command: CommandByteSequence[], ast: ChunkData[]) => {
  for (const [index, node] of ast.entries()) {
    if (command.includes(node.command)) {
      return index;
    }
  }

  return -1;
};

const isPrintingCommand = (command: CommandByteSequence) => {
  return printCommands.includes(command);
};

export const isPrintingWithLineFeedCommand = (command: CommandByteSequence) => {
  return [`${FF}`, `${LF}`, `${CR}`, `${ESC}\x4a`, `${ESC}\x64`].includes(command);
};

const addMissingInitializerCommand = (ast: ChunkData[], commandTree: RuleTree) => {
  const correctedAST: ChunkData[] = [];
  if (ast[0].command !== `${ESC}\x40`) {
    correctedAST.push({
      command: `${ESC}\x40`,
      description: (
        commandTree[ESC]['\x40'] as {
          content: Description;
        }
      ).content[0],
      isCorrected: true,
    });
  }

  correctedAST.push(...ast);
  return correctedAST;
};

// This effectively remove "top padding" of the receipt
const removeAllBeginningPrintWithLineFeedCommand = (ast: ChunkData[]) => {
  const correctedAST: ChunkData[] = [];
  let possibleTitleTextFound = false;

  const printerContext = new PrinterContext();
  for (const [, node, textData] of walkAST(ast, printerContext)) {
    if (!possibleTitleTextFound && isPrintingWithLineFeedCommand(node.command) && textData.length > 0) {
      possibleTitleTextFound = true;
    }

    if (!isPrintingWithLineFeedCommand(node.command) || possibleTitleTextFound) {
      correctedAST.push(node);
    }
  }

  return correctedAST;
};

const fixTextDataPrintCommand = (ast: ChunkData[], commandTree: RuleTree) => {
  const correctedAST: ChunkData[] = [];
  const skipIndex: number[] = [];

  for (const [index, node] of ast.entries()) {
    if (skipIndex.includes(index)) continue;
    correctedAST.push(node);

    if (node.extractedData?.text?.length > 0 && !isPrintingCommand(node.command)) {
      let correctedChunk: ChunkData = {
        command: LF,
        description: (
          commandTree[LF] as {
            content: Description;
          }
        ).content[0],
      };

      const maybeFoundIndex = findCommand(printCommands, ast.slice(index - 1));
      if (maybeFoundIndex >= 0) {
        const printCommand = ast[index - 1 + maybeFoundIndex];
        if (!printCommand.extractedData?.text || printCommand.extractedData?.text.length === 0) {
          correctedChunk = printCommand;
          skipIndex.push(index - 1 + maybeFoundIndex);
        }
      }

      correctedChunk = {
        ...correctedChunk,
        isCorrected: true,
        extractedData: {
          text: node.extractedData.text,
        },
      };

      // This is essentially moving the extracted data text to another chunk
      // which is the corrected chunk
      delete correctedAST[correctedAST.length - 1].extractedData.text;
      correctedAST.splice(correctedAST.length - 1, 0, correctedChunk);
    }
  }

  return correctedAST;
};

const splitOverflowTextData = (ast: ChunkData[], maximumCharacterInOneline: number) => {
  const printerContext = new PrinterContext();
  for (const [index, node, textData] of walkAST(ast, printerContext)) {
    if (isPrintingWithLineFeedCommand(node.command) && textData.length > maximumCharacterInOneline) {
      // eslint-disable-next-line security/detect-non-literal-regexp
      const leastPossibleCharacters = new RegExp(`[\\s\\S]{1,${maximumCharacterInOneline}}`, 'g');
      const newChunkDatas: ChunkData[] = [];

      textData.match(leastPossibleCharacters).forEach((newTextData) => {
        newChunkDatas.push({
          ...node,
          isCorrected: true,
          extractedData: {
            text: encodeTextData(newTextData, printerContext.getTextState().codepage),
          },
        });
      });

      ast.splice(index, 1, ...newChunkDatas);
    }
  }

  return ast;
};

const findPossibleMaximumWidth = (ast: ChunkData[]) => {
  const printerContext = new PrinterContext();

  const charLengths = [];
  for (const [, node, textData] of walkAST(ast, printerContext)) {
    if (
      textData.length > 0 && // Text data is presumably only around 0 - 60 characters in a single line
      textData.length < 60 &&
      isPrintingWithLineFeedCommand(node.command) && // Make sure its a print with line feed command
      printerContext.getTextState().position === 'left' && // Make sure there were no align command previously
      printerContext.getTextState().height === 1 && // Make sure the text height was not altered previously
      printerContext.getTextState().width === 1 && // Make sure the text width was not altered previously
      possiblyAlignedText(textData) // Make sure that this is some sort of aligned text
    ) {
      // Possible maximum character length found
      charLengths.push(textData.length);
    }
  }

  if (charLengths.length > 0) {
    return Math.max(...charLengths);
  }

  // No possible maximum character length found
  return -1;
};

/**
 * Check if certain text data is actually an aligned text via semantic analysis
 * which works by checking the maximum number of how many characters are the same
 * in the corresponding text data, for example these text datas will be counted as
 * aligned text when the threshold is set to 10:
 *
 * "                test              "
 * "              pi-xcels            "
 * "----------------------------------"
 * "                 ^v^              "
 *
 * And 10 is a logical default value here because average actual maximum characters
 * in a pos terminal printer is 32 chars, so having ~30% of the average as default
 * value is cool enough to determine an aligned text in most cases
 */
const possiblyAlignedText = (textData: string, threshold = 10) => {
  return maxSameCharacterSequence(textData) > threshold;
};

// Text is assumed as divider text for when it has all same characters in the string
export const possiblyDividerText = (textData: string, maximumCharacterInOneline: number) => {
  if (textData.split('').every((char) => char === textData[0])) return true;
  else return maxSameCharacterSequence(textData) > maximumCharacterInOneline - 5;
};

export const findNextDividerPosition = (textData: string, maximumCharacterInOneline: number, separator: string[], spacesAmount = 3) => {
  const separatorExist = separator.some((char) => textData.includes(char));
  let isCharFound = false;

  for (const [index, char] of textData.split('').entries()) {
    // Skip spaces in the beginning of the text data
    if (!isCharFound && char !== ' ') {
      isCharFound = true;
    }

    if (isCharFound) {
      if (separatorExist && separator.includes(char)) {
        return index;
      }

      if (char === ' ' && index !== textData.length - 1) {
        if (possiblyDividerText(textData.slice(index, index + spacesAmount), maximumCharacterInOneline)) {
          // Next [spacesAmount] characters is spaces and indicate a possible divider text
          return index;
        }
      }

      if (index >= textData.length - 1) return index;
    }
  }

  return -1;
};

export const trySplitTextData = (
  textData: string,
  analyzerNode: DataChunk[],
  maximumCharacterInOneline: number,
  spacesAmount = 3,
  splittedTexts: {
    text: string;
    endPosition: number;
  }[] = []
): {
  text: string;
  endPosition: number;
}[] => {
  // Split text data using most spaces first algorithm
  while (splittedTexts.length < analyzerNode.length) {
    const lastSplittedTextPos = splittedTexts.length > 0 ? splittedTexts[splittedTexts.length - 1].endPosition : 0;
    const remainingTextData = textData.slice(lastSplittedTextPos);
    let dividerPos = -1;
    let splittedTextIndex = -1;

    // Find next divider position without taking care of separator (that only consist of spaces)
    if (lastSplittedTextPos >= textData.length - 1) {
      for (const [index, text] of splittedTexts.entries()) {
        dividerPos = findNextDividerPosition(text.text, maximumCharacterInOneline, [], spacesAmount);
        if (dividerPos >= text.text.length - 1) {
          dividerPos = -1;
        }

        if (dividerPos !== -1) {
          splittedTextIndex = index;
          break;
        }
      }
    } else {
      dividerPos = findNextDividerPosition(remainingTextData, maximumCharacterInOneline, [], spacesAmount);
    }

    if (dividerPos === -1 || dividerPos >= textData.length - 1) {
      if (spacesAmount === 1) {
        break;
      }

      spacesAmount -= 1;
    }

    if (splittedTextIndex === -1) {
      if (dividerPos !== -1) {
        if (splittedTexts.length > 0) {
          dividerPos += lastSplittedTextPos + 1;
        }

        splittedTexts.push({
          text: textData.slice(lastSplittedTextPos, dividerPos),
          endPosition: dividerPos + 1,
        });
      }
    } else {
      const currentSplittedText = cloneDeep(splittedTexts[splittedTextIndex]);

      splittedTexts[splittedTextIndex] = {
        text: currentSplittedText.text.slice(0, dividerPos),
        endPosition: dividerPos,
      };

      splittedTexts.splice(splittedTextIndex + 1, 0, {
        text: currentSplittedText.text.slice(dividerPos + 1, currentSplittedText.endPosition - 1),
        endPosition: currentSplittedText.endPosition,
      });
    }
  }

  // Validate each splitted text via its data type and/or data regex
  let nodeIndex = 0;
  while (nodeIndex < analyzerNode.length - 1) {
    const changeTextPlacement = (splittedTextMatchedPos: number, matchedText: string, currentIndex: number) => {
      if (splittedTextMatchedPos === 0) {
        splittedTexts[currentIndex] = {
          text: matchedText,
          endPosition: splittedTexts[currentIndex].endPosition - (splittedTexts[currentIndex].endPosition - matchedText.length),
        };

        if (splittedTexts.length < analyzerNode.length) {
          splittedTexts.splice(currentIndex + 1, 0, {
            text: currentSplittedText.text.slice(matchedText.length),
            endPosition: splittedTexts[currentIndex].endPosition,
          });
        } else {
          if (currentIndex + 1 > splittedTexts.length - 1) {
            // It wont make any sense semantically when this happened so skip current
            // text data instead of trying to make another guess, and let the developer
            // write a better analyzer guide at this node
            throw new Error('Need to push to next data while this is supposedly the last data');
          } else {
            splittedTexts[currentIndex + 1].text = currentSplittedText.text.slice(matchedText.length) + splittedTexts[currentIndex + 1];
          }
        }
      } else {
        splittedTexts[currentIndex].text = matchedText;
        if (splittedTexts.length < analyzerNode.length) {
          splittedTexts.splice(currentIndex - 1, 0, {
            text: currentSplittedText.text.slice(0, splittedTextMatchedPos),
            endPosition: splittedTexts[currentIndex].endPosition - (splittedTexts[currentIndex].endPosition - matchedText.length),
          });
        } else {
          if (currentIndex - 1 < 0) {
            // It wont make any sense semantically when this happened so skip current
            // text data instead of trying to make another guess, and let the developer
            // write a better analyzer guide at this node
            throw new Error('Need to push to previous data while this is supposedly the first data');
          } else {
            splittedTexts[currentIndex - 1] = {
              text: splittedTexts[currentIndex - 1] + currentSplittedText.text.slice(0, splittedTextMatchedPos),
              endPosition:
                splittedTexts[currentIndex - 1].endPosition +
                (splittedTexts[currentIndex].endPosition - (splittedTexts[currentIndex].endPosition - matchedText.length)),
            };
          }
        }

        // Recheck previous node
        nextNodeIndex = currentIndex - 1;
      }
    };

    if (nodeIndex > splittedTexts.length - 1) {
      // Cant find any match based on data type nor data regex on current text data,
      // so skip current text data
      throw new Error('Cannot find any matching based on data type nor data regex on current node');
    }

    const node = analyzerNode[nodeIndex];
    const currentSplittedText = cloneDeep(splittedTexts[nodeIndex]);

    let nextNodeIndex = -1;

    if (node.dataType === 'number' && Number.isNaN(Number(currentSplittedText.text))) {
      for (const [charIndex] of currentSplittedText.text.split('').entries()) {
        const splittedTextMatchedLeft = currentSplittedText.text.slice(0, currentSplittedText.text.length - charIndex);
        const splittedTextMatchedRight = currentSplittedText.text.slice(charIndex);
        const splittedTextMatching = [splittedTextMatchedLeft, splittedTextMatchedRight].filter((el) => el !== '' && !Number.isNaN(Number(el)));

        if (splittedTextMatching.length > 0) {
          const splittedTextMatchedPos = currentSplittedText.text.indexOf(splittedTextMatching[0]);
          changeTextPlacement(splittedTextMatchedPos, splittedTextMatchedPos === 0 ? splittedTextMatchedLeft : splittedTextMatchedRight, nodeIndex);
        }
      }
    }

    if (node.dataRegex && currentSplittedText.text.match(new RegExp(node.dataRegex)) === null) {
      const textDataRegexMatch = textData.match(new RegExp(node.dataRegex));

      if (textDataRegexMatch === null) {
        // Regex cant find any match on current text data, so skip current text data
        throw new Error('Cannot find any matching data on current text data based on node data regex');
      } else {
        let matchedText = '';
        if (textDataRegexMatch.length > 1) {
          matchedText = textDataRegexMatch.slice(1).join('');
        } else {
          matchedText = textDataRegexMatch[0];
        }

        const matchedStartPos = textData.indexOf(matchedText);
        const matchedEndPos = matchedStartPos + matchedText.length;

        let isMatchFound = false;
        for (const [index] of analyzerNode.entries()) {
          const currentSplittedText = cloneDeep(splittedTexts[index]);

          for (const [charIndex] of matchedText.split('').entries()) {
            const splittedTextMatchedPos = [
              // Find matching position of matchedText in textData from start to end
              currentSplittedText.text.indexOf(matchedText.slice(0, matchedText.length - charIndex)),
              // Find matching position of matchedText in textData from end to start
              currentSplittedText.text.indexOf(matchedText.slice(charIndex)),
            ].filter((el) => el !== -1);

            if (splittedTextMatchedPos.length > 0) {
              changeTextPlacement(splittedTextMatchedPos[0], matchedText, nodeIndex);
              isMatchFound = true;
              break;
            }
          }

          if (isMatchFound) break;
        }

        if (!isMatchFound) {
          // No match found for given regex on already splitted text data,
          // possible space(s) exist in the regex, rerun all analysis with
          // guided splitted text based on the regex
          return trySplitTextData(textData, analyzerNode, maximumCharacterInOneline, spacesAmount, [
            { text: textData.slice(0, matchedStartPos), endPosition: matchedStartPos },
            {
              text: matchedText,
              endPosition: matchedEndPos,
            },
            { text: textData.slice(matchedEndPos), endPosition: textData.length },
          ]);
        }
      }
    }

    if (nextNodeIndex !== -1) {
      nodeIndex = nextNodeIndex;
      nextNodeIndex = -1;
    } else {
      nodeIndex += 1;
    }
  }

  return splittedTexts;
};

export const textToKeyValue = (
  textData: string,
  maximumCharacterInOneline: number,
  analyzerNode: AnalyzerNode = undefined,
  separator = [':', '=']
): Record<string, any> => {
  const objResult = {};
  textData = textData.trim();

  if (analyzerNode) {
    if (Array.isArray(analyzerNode)) {
      analyzerNode = analyzerNode as DataChunk[];
      try {
        const splittedTexts = trySplitTextData(textData, analyzerNode, maximumCharacterInOneline);

        // Assign resulting object of key value pair from each splitted text
        for (const [index, node] of analyzerNode.entries()) {
          const object = textToKeyValue(splittedTexts[index].text, maximumCharacterInOneline, node);
          Object.assign(objResult, object);
        }

        return objResult;
      } catch {
        return {};
      }
    } else if (analyzerNode.chunkType === 'dataChunk') {
      analyzerNode = analyzerNode as DataChunk;
      const value = analyzerNode.isKeyValuePair
        ? analyzerNode.divider
          ? Object.values(textToKeyValue(textData, maximumCharacterInOneline, undefined, [analyzerNode.divider])).length > 0
            ? Object.values(textToKeyValue(textData, maximumCharacterInOneline, undefined, [analyzerNode.divider]))[0]
            : null
          : Object.values(textToKeyValue(textData, maximumCharacterInOneline)).length > 0
          ? Object.values(textToKeyValue(textData, maximumCharacterInOneline))[0]
          : null
        : textData;

      if (analyzerNode.dataType === 'number' && Number.isNaN(Number(value))) {
        // Value doesnt match given dataType in the analyzerNode
        return {};
      }

      if (analyzerNode.dataRegex && value.match(new RegExp(analyzerNode.dataRegex)) === null) {
        // Value doesnt match given dataRegex in the analyzerNode
        return {};
      }

      // Value match dataType and dataRegex if any, so
      // return the key value pair if we should not ignore it
      if (!analyzerNode.ignore) {
        return {
          [analyzerNode.node]: value,
        };
      }

      return {};
    }
  }

  const dividerPos = {
    startPosition: -1,
    endPosition: -1,
  };

  // Try searching with reduced spaces amount when the divider is actually spaces with less than 3 chars
  for (let i = 3; i > 1; i--) {
    dividerPos.startPosition = findNextDividerPosition(textData, maximumCharacterInOneline, separator, i);
    if (dividerPos.startPosition !== -1) break;
  }

  // Reset dividerPos if its on the end of textData
  // (this is due to the findNextDividerPosition fallback value)
  if (dividerPos.startPosition >= textData.length - 1) {
    dividerPos.startPosition = -1;
  }

  // Try searching by getting the minimum character count position that is not
  // a letter and not a number
  if (dividerPos.startPosition === -1) {
    const charCounts: Record<
      string,
      {
        count: number;
        position: number;
      }
    > = {};

    for (const [index, char] of textData.split('').entries()) {
      if (char in charCounts) {
        charCounts[char].position += 1;
      } else {
        charCounts[char] = {
          count: 0,
          position: index,
        };
      }
    }

    let minimumCount = Infinity;
    for (const el of Object.values(charCounts)) {
      if (el.count < minimumCount) minimumCount = el.count;
    }

    const minimumCountCharPositions: number[] = [];
    for (const [char, el] of Object.entries(charCounts)) {
      if (el.count === minimumCount) {
        // Check if its not a letter and not a number, then its probably a divider
        if (char.toLowerCase() === char.toUpperCase() && Number.isNaN(Number(char))) {
          minimumCountCharPositions.push(el.position);
        }
      }
    }

    dividerPos.startPosition = minimumCountCharPositions[0];

    // Multiple possible divider found, merge those if possible
    if (minimumCountCharPositions.length > 1) {
      for (const [index, position] of minimumCountCharPositions.entries()) {
        if (index !== minimumCountCharPositions.length - 1) {
          const offsetToLastPosition = Math.abs(position - minimumCountCharPositions[index + 1]);
          if (offsetToLastPosition > 1) {
            dividerPos.startPosition = position;
          } else {
            dividerPos.endPosition = position;
          }
        }
      }
    }
  }

  const key = textData.slice(0, dividerPos.startPosition).trim();
  const value = textData.slice(dividerPos.endPosition === -1 ? dividerPos.startPosition + 1 : dividerPos.endPosition + 1).trim();

  if (key !== '' && value !== '') {
    objResult[key.toLowerCase().replace(' ', '-')] = value;
  }

  return objResult;
};

export const fixParsedEscPosAST = (ast: ChunkData[], commandTree: RuleTree) => {
  let correctedAST: ChunkData[] = cloneDeep(ast);
  correctedAST = addMissingInitializerCommand(correctedAST, commandTree);
  correctedAST = fixTextDataPrintCommand(correctedAST, commandTree);

  // TODO: Add a width config is better than this but this is also very handy in most cases
  //       so put a check here later that if a config exist then use config, if not, then use
  //       this one
  const possibleMaximumWidth = findPossibleMaximumWidth(correctedAST);
  if (possibleMaximumWidth > 0) {
    correctedAST = splitOverflowTextData(correctedAST, possibleMaximumWidth);
  }

  correctedAST = removeAllBeginningPrintWithLineFeedCommand(correctedAST);

  return {
    ast: correctedAST,
    possibleMaxWidth: possibleMaximumWidth,
  };
};
